.card-easy-10 {
  $origin: &;
  position: relative;
  display: flex;
  flex-direction: column-reverse;
  justify-content: space-between;
  padding: 44px;
  height: 100%;
  gap: 20px;

  &__container {
    display: grid;
    gap: 8px;
  }

  &__number {
    position: absolute;
    top: 0;
    right: 0;
    padding: 44px;

    font-family: var(--font-family);
    font-weight: 400;
    font-size: px-to-rem(12);
    line-height: 130%;
    text-align: center;
    color: var(--primary-color-normal);
  }

  &__image {
    border-radius: 30px;
    width: 100%;
    height: 290px;
    object-fit: cover;
  }

  &__title {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: px-to-rem(36);
    line-height: 120%;
    text-transform: uppercase;
    color: var(--text-talee-title);

    & i {
      color: var(--text-talee-accent);
    }
  }

  &__text {
    font-family: var(--second-family);
    font-weight: 400;
    font-size: px-to-rem(18);
    line-height: 130%;
    color: var(--text-talee-light);
  }

  &--reverse {
    flex-direction: column;

    #{$origin}__number {
      padding-top: 44px + 290 + 20;
    }
  }
}
