.card-block-9 {
  $origin: &;
  display: flex;
  flex-direction: column-reverse;
  align-items: center;
  gap: 16px;

  &__image {
    border-radius: 20px;
    width: 256px;
    height: 246px;
  }

  &__title {
    font-family: var(--second-family);
    font-weight: 400;
    font-size: px-to-rem(22);
    line-height: 120%;
    text-align: center;
    color: var(--text-talee-light);

    & b {
      font-weight: 700;
      line-height: 130%;
      color: var(--primary);
    }
  }
}
