.button {
  $origin: &;

  min-height: 56px;
  width: 100%;

  @include flex-center;

  gap: 10px;
  padding: 0 26px;

  background: var(--accent);
  border: 1px solid var(--white);
  color: var(--white);
  border-radius: 45px;

  box-shadow: 0 3px 4px 0 rgb(31 33 29 / 11%);

  text-wrap: nowrap;
  text-decoration: none;
  cursor: pointer;

  transition: all var(--transition-time);

  &__text {
    font-family: var(--font-family);
    font-weight: 500;
    font-size: px-to-rem(18);
    line-height: 100%;
    letter-spacing: 0.01em;
    text-align: center;
  }

  &__image {
    display: inline-block;
  }

  &__icon {
    & svg {
      @include size(14px);
    }

    & > * * {
      stroke: var(--white);
    }
  }

  /* Effects */

  &--light {
    background: var(--white);
    border: 1px solid var(--accent);
    color: var(--accent);

    &:link,
    &:visited {
      color: var(--accent);
    }

    & * * {
      stroke: var(--accent);
    }
  }

  &--light-second {
    background: var(--white);
    border: 1px solid var(--primary-blue);
    color: var(--primary-blue);

    &:link,
    &:visited {
      color: var(--primary-blue);
    }

    & * * {
      stroke: var(--primary-blue);
    }
  }

  &--blue {
    background: var(--primary-blue);
    border: 1px solid var(--white);
    color: var(--white);

    #{$origin}__image {
      filter: drop-shadow(0 0 2px var(--primary-blue))
        drop-shadow(0 0 4px var(--primary-blue));
    }
  }

  &--phone {
    justify-content: flex-end;
    flex-direction: row-reverse;
    padding-inline: 10% 25%;
    border: 2px solid var(--primary);
    box-shadow: 0 3px 4px 0 rgb(31 33 29 / 11%);
    background: var(--text-talee-white);
    gap: 10%;

    #{$origin}__image {
      @include size(16px);
    }

    #{$origin}__text {
      @include flex-center;
      gap: 5px;

      font-family: var(--font-family);
      font-weight: 500;
      font-size: px-to-rem(23);
      letter-spacing: 0.01em;
      color: var(--text-talee-dark);
    }

    &::after {
      content: "       ";
      display: inline-block;
      text-wrap: wrap;
      text-align: start;

      font-family: var(--second-family);
      font-weight: 400;
      font-size: px-to-rem(15);
      line-height: 100%;
      color: var(--text-talee-dark);
      max-width: 170px;
      min-width: 162px;
    }

    @include toDesktop {
      #{$origin}__image {
        @include size(13px);
      }
    }

    @include toLaptop {
      #{$origin}__image {
        @include size(12px);
      }
    }

    @include toTablet {
      justify-content: center;
      padding-inline: 0;
      gap: 8px;

      #{$origin}__image {
        // display: none;
        @include size(11px);
      }

      &::after {
        content: "";
        display: inline-block;
        // background-image: url("../images/icons/phone.svg");
        // background-repeat: no-repeat;
        // background-size: contain;

        @include min-size(14px);
      }
    }
  }

  &--phone-blue {
    filter: hue-rotate(180deg);
  }

  &--phone-mini {
    position: relative;
    justify-content: center;
    flex-direction: row-reverse;
    border: 2px solid var(--primary);
    box-shadow: 0 3px 4px 0 rgb(31 33 29 / 11%);
    background: var(--text-talee-white);
    gap: 10%;

    #{$origin}__image {
      @include size(16px);
    }

    #{$origin}__text {
      @include flex-center;
      gap: 5px;

      font-family: var(--font-family);
      font-weight: 500;
      font-size: px-to-rem(23);
      letter-spacing: 0.01em;
      color: var(--text-talee-dark);
    }

    &::after {
      position: absolute;
      content: "       ";
      bottom: 100%;
      display: inline-block;
      text-wrap: wrap;
      text-align: start;
      padding-block: 16px;

      font-family: var(--second-family);
      font-weight: 400;
      font-size: px-to-rem(15);
      line-height: 100%;
      color: var(--text-talee-dark);
    }

    @include toDesktop {
      #{$origin}__image {
        @include size(13px);
      }
    }

    @include toLaptop {
      #{$origin}__image {
        @include size(12px);
      }
    }

    @include toTablet {
      &::after {
        display: none;
      }

      #{$origin}__image {
        @include size(11px);
      }
    }
  }

  &--small {
    min-height: 36px;

    #{$origin}__text {
      font-family: var(--font-family);
      font-weight: 500;
      font-size: px-to-rem(14);
      line-height: 80%;
      letter-spacing: 0.01em;
      text-transform: uppercase;
      text-align: center;
    }

    #{$origin}__icon {
      & svg {
        @include size(10px);
        transform: translateY(-1px);
      }
    }
  }

  &--nowrap {
    width: max-content;
    text-wrap: nowrap;
  }

  /* Hovers */

  @include hover {
    background-color: var(--white);
    border-color: var(--accent);
    color: var(--accent);

    & * * {
      stroke: var(--accent);
    }
  }

  &--light {
    @include hover {
      background: var(--accent);
      border-color: var(--white);
      color: var(--white);

      &:link,
      &:visited {
        color: var(--white);
      }

      & * * {
        stroke: var(--white);
      }
    }
  }

  &--light-second {
    @include hover {
      background: var(--primary-blue);
      border-color: var(--white);
      color: var(--white);

      &:link,
      &:visited {
        color: var(--white);
      }

      & * * {
        stroke: var(--white);
      }
    }
  }

  &--blue {
    @include hover {
      background: var(--white);
      border-color: var(--primary-blue);
      color: var(--primary-blue);

      #{$origin}__icon {
        & svg {
          @include size(14px);
        }

        & > * * {
          stroke: var(--primary-blue);
        }
      }
    }
  }

  &--phone {
    @include hover {
      filter: drop-shadow(0 0 15px var(--primary-30p));
    }
  }

  &--phone-blue {
    @include hover {
      filter: drop-shadow(0 0 15px var(--primary-30p)) hue-rotate(180deg);
    }
  }

  &--phone-mini {
    @include hover {
      filter: drop-shadow(0 0 15px var(--primary-30p));
    }
  }
}

@import "./button-adaptive";
