.section-how-its-work {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 20px;

  &__block-1 {
    padding: 40px;
    border-radius: 30px;
    -webkit-backdrop-filter: blur(12px);
    backdrop-filter: blur(12px);
    box-shadow: 0 0 14px 0 rgb(180 174 167 / 19%);
    background: rgb(255 255 255 / 95%);

    display: flex;
    flex-direction: column;
    gap: 20px;
  }

  &__block-2 {
    border-radius: 30px;
    overflow: hidden;
    height: 100%;
    max-height: 625px;
  }

  &__container {
    display: grid;
    grid-template-columns: max-content 1fr;
    gap: 20px;
  }

  &__image-1 {
    border-radius: 25px;
    object-fit: cover;
  }

  &__image-2 {
    border-radius: 20px;
    margin-bottom: 12px;
    object-fit: cover;
  }

  &__text {
    font-family: var(--second-family);
    font-weight: 400;
    font-size: px-to-rem(18);
    line-height: 130%;
    text-align: justify;
    color: var(--text-talee-dark);

    & > strong {
      font-weight: 900;
    }

    & > b {
      color: var(--primary);
      font-weight: 400;
    }
  }

  @include toDesktop {
    gap: 15px;

    &__block-1 {
      padding: 20px;
      gap: 15px;
    }

    &__block-2 {
      height: 100%;
      max-height: 100%;
    }

    &__text {
      font-size: px-to-rem(16);
    }

    &__image-1 {
      width: 180px;
      height: 225px;
    }

    &__container {
      gap: 15px;
    }
  }

  @include toLaptop {
    grid-template-columns: 692px 1fr;
  }

  @include toTablet {
    grid-template-columns: 1fr;

    &__block-2 {
      height: 336px;
    }
  }

  @include toPhone {
    &__container {
      grid-template-columns: 1fr;
    }

    &__content {
      display: grid;
      grid-template-columns: 1fr 1fr;
      gap: 10px;
    }

    &__block-1 {
      padding: 10px;
      padding-block: 20px;
    }

    &__block-2 {
      height: 270px;
    }

    &__image-1,
    &__image-2 {
      @include size(100%);
      height: 160px;
    }
  }
}
