.section-block {
  $origin: &;
  position: relative;
  z-index: 1;

  &__title-container {
    // display: flex;
    // justify-content: space-between;
    // align-items: start;
    margin-bottom: 60px;

    display: grid;
    grid-template-columns: max-content 1fr;
    gap: 20px;
  }

  &__content-container {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 20px;
  }

  &--background-1 {
    padding: 54px 30px;
    border-radius: 30px;
    background-image: url("../images/decor/decor_1.jpg");
    background-size: cover;
    background-repeat: no-repeat;
    z-index: 1;

    @include toDesktop {
      padding: 30px;

      #{$origin}__content-container {
        gap: 15px;
      }
    }

    @include toLaptop {
      padding: 30px;

      #{$origin}__content-container {
        grid-template-columns: 1fr 1fr !important;
      }
    }

    @include toTablet {
      padding: 26px 24px;

      #{$origin}__content-container {
        grid-template-columns: 1fr !important;
      }
    }

    @include toPhone {
      padding: 12px 20px;
    }
  }

  &--background-2 {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 30px;
    height: 750px;
    padding-top: 60px;
    border-radius: 30px;
    background-image: url("../images/decor/decor_7.jpg");
    z-index: 1;

    @include toDesktop {
      padding: 30px;

      #{$origin}__content-container {
        gap: 15px;
      }
    }

    @include toLaptop {
      padding: 30px;

      #{$origin}__content-container {
        grid-template-columns: 1fr 1fr !important;
      }
    }

    @include toTablet {
      height: 520px;
      padding: 26px 24px;

      #{$origin}__content-container {
        grid-template-rows: repeat(2, 100px);
      }
    }

    @include toPhone {
      height: 100%;
      padding: 12px 20px;
      padding-bottom: 20px;

      #{$origin}__content-container {
        grid-template-columns: 1fr !important;
        grid-template-rows: repeat(4, 100px);
      }
    }
  }

  &--columns-1 {
    #{$origin}__content-container {
      grid-template-columns: repeat(1, 1fr);
    }
  }

  &--columns-2 {
    #{$origin}__content-container {
      grid-template-columns: repeat(2, 1fr);
    }
  }

  &--columns-3 {
    #{$origin}__content-container {
      grid-template-columns: repeat(3, 1fr);
    }
  }

  &--columns-4 {
    #{$origin}__content-container {
      grid-template-columns: repeat(4, 1fr);
    }
  }

  &--slider-container {
    #{$origin}__content-container {
      display: block;
      width: 100%;
      max-width: 1234px;
      margin: auto;
    }
  }

  &--more-about-talee {
    @include toTablet {
      #{$origin}__content-container {
        grid-template-columns: 1fr;
      }
    }
  }

  &--licuvanya {
    @include toPhone {
      #{$origin}__content-container {
        grid-template-columns: 1fr;
      }
    }
  }

  /* Adaptive */

  @include toTablet {
    #{$origin}__title-container {
      grid-template-columns: 1fr;
      margin-bottom: 30px;
    }
  }

  @include toPhone {
    #{$origin}__content-container {
      grid-template-columns: 1fr;
    }
  }
}
