.card-block-4 {
  $origin: &;

  @include flex-center;
  justify-content: start;
  align-items: center;

  flex-direction: column;
  gap: 20px;

  padding: 50px 60px;
  border-radius: 30px;
  -webkit-backdrop-filter: blur(7px);
  backdrop-filter: blur(7px);
  box-shadow: 0 0 18px 0 rgb(180 174 167 / 20%);
  background: rgb(255 255 255 / 65%);

  &__container {
    display: flex;
    flex-direction: column;
    gap: 15px;
  }

  &__title {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: px-to-rem(24);
    line-height: 123%;
    letter-spacing: 0.01em;
    text-transform: uppercase;
    text-align: right;
    color: #ea8529;
  }

  &__text {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: px-to-rem(18);
    line-height: 110%;
    text-align: right;
    color: #62500f;
  }

  @include toDesktop {
    padding: 40px 30px;
  }

  @include toPhone {
    padding: 20px;

    &__title {
      font-size: px-to-rem(18);
    }
  }

  // Effects

  &--blue {
    #{$origin}__title {
      color: var(--second-material-600);
    }
  }
}
