.card-block-new {
  $origin: &;
  display: flex;
  flex-direction: column-reverse;
  position: relative;
  height: 700px;

  // max-height: max-content;

  &__image {
    @include no-event;
    position: absolute;
    border-radius: 20px;
    overflow: hidden;
    width: 100%;

    height: 100%;
    object-fit: cover;
    z-index: -1;
  }

  &__title {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: px-to-rem(48);
    line-height: 120%;
    text-transform: uppercase;
    color: #fff8f0;

    margin-bottom: 12px;
  }

  &__text {
    font-family: var(--second-family);
    font-weight: 400;
    font-size: px-to-rem(24);
    line-height: 130%;
    color: #fff8f0;

    @include text-ellipsis-lines;
    margin-bottom: 30px;
  }

  &__container {
    position: relative;
    z-index: 1;
    padding: 64px;
    width: 100%;
    max-width: 772px;

    &::after {
      content: attr(data-content);
      display: block;
      position: absolute;

      font-family: var(--font-family);
      font-weight: 400;
      font-size: px-to-rem(20);
      line-height: 120%;
      color: var(--primary-surface-white);
      z-index: 3;

      top: 32px;
      left: 32px + 32 + 30;
    }

    &::before {
      content: "";
      display: block;
      position: absolute;
      z-index: 2;

      @include size(24px);
      background-image: url("../images/icons/calendar.svg");

      top: 32px;
      left: 32px + 32;
    }
  }

  &--normal {
    height: 556px;

    #{$origin}__title {
      font-size: px-to-rem(36);
    }

    #{$origin}__text {
      font-size: px-to-rem(22);
    }

    #{$origin}__container {
      max-width: 100%;
      padding: 40px;

      &::after {
        top: 0;
        left: 40px + 30;
      }

      &::before {
        top: 0;
        left: 40px;
      }
    }
  }
}
