.rating {
  $origin: &;
  display: flex;

  &__star {
    @include min-size(24px);
    @include size(24px);
    display: block;
    background-image: url("../images/icons/star-gray.svg");
    background-repeat: no-repeat;
    background-position: center;
    cursor: pointer;

    transition: all var(--transition-time);

    &.hovered {
      background-image: url("../images/icons/star-gold.svg");
      filter: opacity(0.35) !important;

      &:hover {
        transform: scale(1.2) translateY(-2px) !important;
        filter: opacity(0.5) !important;
      }

      &:active {
        transform: scale(1.2) rotate(145deg) !important;
      }
    }

    &.selected {
      background-image: url("../images/icons/star-gold.svg");
      filter: opacity(1) !important;
    }
  }

  &--big {
    gap: 32px;

    #{$origin}__star {
      @include min-size(80px);
      @include size(80px);
      background-size: contain;

      &.hovered {
        &:hover {
          transform: scale(1.05) translateY(-2px) !important;
          filter: opacity(0.5) !important;
        }

        &:active {
          transform: scale(0.98) translateY(4px) !important;
        }
      }
    }
  }
}
