.statistic {
  display: inline-block;
  width: 100%;
  max-width: max-content;

  &__number {
    font-family: var(--second-family);
    font-weight: 500;
    font-size: px-to-rem(80);
    line-height: 100%;
    letter-spacing: 0.01em;
    color: var(--light-blue);

    margin-bottom: 8px;
  }

  &__text {
    line-height: 120%;
    letter-spacing: 0.02em;
  }

  @include toPhone {
    &__number {
      font-size: px-to-rem(60);
    }

    &__text {
      font-size: px-to-rem(14);
    }
  }
}
