.card-easy-8 {
  $origin: &;
  padding: 32px;

  &__container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 24px;
    margin-bottom: 16px;
  }

  &__image {
    @include shadow;
    @include min-size(78px);
    padding: 18px;
    border-radius: 40%;
    background-color: var(--primary-surface-white-hover);
  }

  &__title {
    font-family: var(--second-family);
    font-weight: 400;
    font-size: px-to-rem(22);
    line-height: 130%;
    text-transform: uppercase;
    color: var(--text-talee-title);
  }

  &__text {
    font-family: var(--second-family);
    font-weight: 400;
    font-size: px-to-rem(16);
    line-height: 130%;
    color: var(--text-talee-light);
  }
}
