.card-block-5 {
  $origin: &;

  display: flex;
  align-items: center;
  gap: 32px;
  padding: 30px 24px;

  &__image {
    @include shadow;
    @include min-size(118px);
    @include size(118px);
    background-color: var(--primary-surface-white-hover);
    border-radius: 100%;
    padding: 25px;
  }

  &__title {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: px-to-rem(24);
    line-height: 120%;
    text-transform: uppercase;
    color: var(--text-tale-title);

    margin-bottom: 8px;
  }

  &__text {
    font-family: var(--second-family);
    font-weight: 400;
    font-size: px-to-rem(16);
    line-height: 130%;
    color: var(--text-talee-light);

    @include text-ellipsis-lines;

    margin-bottom: 20px;
  }

  &__block {
    display: flex;
    flex-direction: column;
    align-items: end;
  }

  &--space {
    #{$origin}__container {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      height: 100%;
    }

    #{$origin}__text {
      margin: 0;
    }
  }

  &--mini {
    align-items: start;
    height: 230px;

    // height: 100%;

    #{$origin}__text {
      @include text-ellipsis-lines(4);
      margin-bottom: 15px;
    }

    #{$origin}__image {
      @include size(100px);
      @include min-size(100px);
      border-radius: 100%;
      box-shadow: 0 0 14px 0 rgb(180 174 167 / 19%);
    }

    #{$origin}__container {
      height: 100%;
      display: grid;
      grid-template-rows: max-content 1fr max-content;
    }
  }
}
