.section-solution {
  z-index: 2;

  &__label {
    position: absolute;
    top: 0;
    left: 0;
  }

  &__title-container {
    display: flex;
    align-items: start;
    margin-bottom: 44px;

    position: relative;
    justify-content: center;
  }

  &__title-block {
    max-width: 660px;
    position: relative;
    z-index: 2;
  }

  &__title {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: px-to-rem(36);
    line-height: 110%;
    text-transform: uppercase;
    text-align: center;
    color: #62500f;

    & > b {
      color: #ea8529;
    }
  }

  &__content-container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 20px;
  }

  &__content {
    display: flex;
    flex-direction: column;
    gap: 60px;
  }

  &__cards {
    display: none;
  }

  &__buttons {
    display: none;
    z-index: 2;
  }

  @include toTablet {
    &__title-container {
      flex-direction: column;
      width: 100%;
    }

    &__content {
      gap: 20px;
    }

    &__label {
      position: relative;
    }

    &__title {
      text-align: end;
      font-size: px-to-rem(28);
      width: 110%;
    }

    &__circle {
      display: flex;
      align-items: center;
      gap: 20px;
    }

    &__cards {
      @include size(100%);
      display: block;
    }

    &__buttons {
      display: flex;
      flex-direction: column;
      width: 100%;
      gap: 10px;

      &__top,
      &__bottom {
        display: flex;
        gap: 10px;
      }

      & .button-accordion {
        width: 100%;

        &__container {
          width: 100%;
        }
      }
    }
  }

  @include toPhone {
    &__title {
      font-size: px-to-rem(20);
      width: 100%;
      text-align: start;
    }

    &__buttons {
      gap: 8px;

      &__top,
      &__bottom {
        flex-direction: column;
        gap: 8px;
      }
    }

    &__circle {
      flex-direction: column;
      margin-top: -30px;
    }

    &__cards {
      margin-top: -120px;
    }
  }
}

.circle-pointer {
  @include flex-center;
  position: relative;

  &__line {
    position: absolute;
    border-bottom: 2px dashed #b0a583;
    width: 944px;

    @include toDesktop {
      width: 650px;
    }

    @include toLaptop {
      width: 550px;
    }
  }

  &__container {
    position: relative;
    width: max-content;
    height: max-content;
    border: 2px dashed rgb(176 165 131 / 50%);
    border-radius: 100%;
    padding: 30px;
    z-index: 1;
    background-color: var(--bg);
  }

  &__image {
    @include size(538px);
    overflow: hidden;
    border-radius: 100%;
  }

  &__item {
    // left
    &--1 {
      transform: translate(-500px, -260px);
    }

    &--2 {
      transform: translate(-640px, 0);
    }

    &--3 {
      transform: translate(-500px, 260px);
    }

    // right
    &--4 {
      transform: translate(500px, -260px);
    }

    &--5 {
      transform: translate(640px, 0);
    }

    &--6 {
      transform: translate(500px, 260px);
    }

    @include toDesktop {
      // left
      &--1 {
        transform: translate(-420px, -200px);
      }

      &--2 {
        transform: translate(-500px, 0);
      }

      &--3 {
        transform: translate(-420px, 200px);
      }

      // right
      &--4 {
        transform: translate(420px, -200px);
      }

      &--5 {
        transform: translate(500px, 0);
      }

      &--6 {
        transform: translate(420px, 200px);
      }
    }

    @include toLaptop {
      // left
      &--1 {
        transform: translate(-335px, -160px);
      }

      &--2 {
        transform: translate(-420px, 0);
      }

      &--3 {
        transform: translate(-335px, 160px);
      }

      // right
      &--4 {
        transform: translate(335px, -160px);
      }

      &--5 {
        transform: translate(420px, 0);
      }

      &--6 {
        transform: translate(335px, 160px);
      }
    }
  }

  /* Adaptive */

  @include toDesktop {
    &__container {
      padding: 22px;
    }

    &__image {
      @include size(404px);
    }
  }

  @include toLaptop {
    &__container {
      padding: 18px;
    }

    &__image {
      @include size(330px);
    }
  }

  @include toTablet {
    &__container {
      padding: 16px;
    }

    &__image {
      @include size(270px);
    }

    &__block {
      display: none;
    }

    &__line {
      display: none;
    }
  }

  @include toPhone {
    &__container {
      padding: 14px;
    }

    &__image {
      @include size(248px);
    }
  }
}

.anim-rotate {
  animation-name: anim-rotate;
  animation-duration: 200s;
  animation-timing-function: linear;
  animation-iteration-count: infinite;

  &--reverse {
    animation-name: anim-rotate;
    animation-duration: 100s;
    animation-timing-function: linear;
    animation-iteration-count: infinite;
    animation-direction: reverse;
  }

  &--stop {
    animation-name: anim-rotate;
    animation-duration: 200s;
    animation-timing-function: linear;
    animation-iteration-count: infinite;
  }
}

@keyframes anim-rotate {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}
