.card-easy-6 {
  $origin: &;
  position: relative;
  padding: 40px;

  @include flex-center;
  background-color: var(--white);

  &__number {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: px-to-rem(15);
    line-height: 130%;
    text-align: center;
    color: var(--primary-color-normal);

    position: absolute;
    top: 0;
    left: 0;
    padding: 40px;
  }

  &__container {
    @include flex-center;
    gap: 18px;
  }

  &__image {
    @include min-size(128px);
    @include size(128px);
  }

  &__title {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: px-to-rem(28);
    line-height: 120%;
    text-transform: uppercase;
    color: var(--text-talee-title);
    width: 100%;
    max-width: 440px;
  }
}
