.title-block-4 {
  display: flex;
  flex-direction: column-reverse;
  text-align: start;

  &__main-text {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: px-to-rem(40);
    line-height: 113%;
    letter-spacing: 0.01em;
    text-transform: uppercase;
    color: var(--primary);
    margin-bottom: 10px;
  }

  &__text {
    // width: 100%;
    width: 254px;
    font-family: var(--second-family);
    font-weight: 400;
    font-size: px-to-rem(18);
    line-height: 130%;
    text-align: justify;
    color: var(--dark-gray);
  }

  &__container {
  }
}
