@mixin setting-main-photo {
  @include flex-center;
  flex-direction: column;
  box-sizing: content-box;
  border-radius: 100%;
  aspect-ratio: 1;
}

.main-photo {
  $origin: &;

  &__block {
    position: absolute;
    z-index: 1;
  }

  &__0 {
    @include setting-main-photo;
    @include size(100%);
    position: relative;
    background-color: rgb(180 174 167 / 6%);
  }

  &__1 {
    @include setting-main-photo;
    @include size(85%);
    background-color: rgb(180 174 167 / 6%);
  }

  &__2 {
    @include setting-main-photo;
    @include size(80%);
    background-color: rgb(180 174 167 / 6%);
  }

  &__3 {
    @include setting-main-photo;
    @include size(75%);
    background-color: rgb(98 80 15 / 2%);
  }

  &--blue {
    background-color: rgb(41 147 221 / 6%);

    #{$origin}__1 {
      @include size(85%);
      background-color: rgb(41 147 221 / 6%);
    }

    #{$origin}__2 {
      @include size(80%);
      background-color: rgb(41 147 221 / 6%);
    }

    #{$origin}__3 {
      @include size(75%);
      background-color: rgb(41 147 221 / 2%);
    }
  }

  &--orange {
    background-color: rgba(221, 134, 41, 0.06);

    #{$origin}__1 {
      @include size(85%);
      background-color: rgba(221, 134, 41, 0.06);
    }

    #{$origin}__2 {
      @include size(80%);
      background-color: rgba(221, 134, 41, 0.06);
    }

    #{$origin}__3 {
      @include size(75%);
      background-color: rgba(221, 134, 41, 0.06);
    }
  }
}
