.section-form {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 20px;

  &__container {
    flex-direction: row;
    padding-inline: 80px;
    gap: 20px;
  }

  &__title {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: px-to-rem(28);
    line-height: 110%;
    letter-spacing: 0.01em;
    text-transform: uppercase;
    text-align: left;
    color: var(--text-tale-title);
  }

  &--blue {
    filter: hue-rotate(180deg);
  }

  @include toDesktop {
  }

  @include toLaptop {
    &__container {
      padding-inline: 40px;
    }

    &__title {
      font-size: px-to-rem(22);
    }
  }

  @include toTablet {
    display: flex;
    flex-direction: column;

    &__container {
      padding: 30px;
    }
  }

  @include toPhone {
    padding: 0;

    &__title {
      font-size: px-to-rem(16);
    }
  }
}
