:root { --scroll-bar-size: 1.15vw }

::-webkit-scrollbar {
  width: var(--scroll-bar-size);
  height: var(--scroll-bar-size);

  &-button {
    width: 0;
    height: 0;
  }

  &-thumb {
    background: var(--primary);
    border: 0.25vw solid var(--white);
    border-radius: 100px;
    box-shadow: inset -4px -4px 10px -1px rgb(0 0 0 / 15%);

    &:hover {
      background: var(--primary-50p);
    }

    &:active {
      background: var(--primary-color-normal-active);
    }
  }


  &-track {
    background: var(--white);
    border: 0 none var(--white);
    border-radius: 100px;
    box-shadow: inset 0 0 1px 1px rgb(0 0 0 / 7.5%);

    &:hover { background: var(--white) }
    &:active { background: var(--white) }
  }

  &-corner {
    background: transparent;
  }
}








