.section-main-block {
  $origin: &;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  margin-bottom: -40px;

  &__title-container {
    padding-top: 210px;
    padding-bottom: 60px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    gap: 20px;

    height: 100%;
    z-index: 1;

    &__content-first {
      display: flex;
      flex-direction: column;
      gap: 20px;
      margin-left: 125px;
      margin-right: -30px;
      min-width: 982px;
    }
  
    &__title {
      font-family: var(--font-family);
      font-weight: 400;
      font-size: px-to-rem(80);
      line-height: 101%;
      letter-spacing: 0.02em;
      text-transform: uppercase;
      color: #62500f;
      text-wrap: nowrap;
  
      & > b {
        color: var(--primary);
      }
    }
  
    &__text {
      width: 100%;
      max-width: 500px;
  
      font-family: var(--second-family);
      font-weight: 400;
      font-size: px-to-rem(24);
      line-height: 130%;
      color: #5d5955;
    }
  
    &__button {
      width: 100%;
      max-width: 500px;
    }

    //
  
    &__content-container {
      margin-top: -144px;
      margin-left: -200px;
    }
  
    &__content {
      display: grid;
      grid-template-columns: max-content 1fr;
      gap: 20px;
      width: 762px;
    }
  
    &__title-mini {
      padding-left: 20px;
      margin-bottom: -10px;
    }
  
    &__video {
      width: 292px;
      height: 170px;
    }
  }

  &__content-container {
    margin-top: -125px;
    margin-left: -180px;

    &__photo {
      @include size(524px);
      position: relative;
      overflow: hidden;
      border-radius: 100%;
      background: #fff0e2;
  
      -webkit-backdrop-filter: blur(20px);
      backdrop-filter: blur(20px);
      box-shadow: 0 0 15px 0 rgb(180 174 167 / 20%);
      z-index: 2;
  
      &__index .main-photo__block {
        z-index: 1;
      }
    }
  
    &__card-photo {
      @include shadow;
      width: 388px;
      position: absolute;
      border-radius: 30px;
      overflow: hidden;
      left: -296px;
      bottom: -56px;
  
      & > * {
        @include size(100%);
        align-items: end;
      }
  
      & .card-block-4 {
        align-items: center;
      }
  
      & .card-block-4__container {
        // padding-right: 64px;
      }
    }
  }

  // Effects

  &--blue {
    #{$origin}__title-container {
      &__text {
        color: var(--text-piro-ttle);
      }

      &__title {
        color: var(--text-piro-ttle);

        & > b {
          color: var(--text-piro-accent);
        }
      }
    }

    #{$origin}__content-container {
      &__photo {
        @include size(596px);
        overflow: inherit;
        background: #cae5f7;
      }
    }
  }
}
