.section-shlem {
  $origin: &;
  z-index: 0;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;

  &__title-container {
    display: flex;
    align-items: start;
    margin-bottom: 44px;

    position: absolute;
    justify-content: center;
    top: 0;
    width: 100%;
  }

  &__label {
    position: absolute;
    top: 0;
    left: 0;
  }

  &__title {
    max-width: 660px;
    position: relative;
    z-index: 2;

    font-family: var(--font-family);
    font-weight: 400;
    font-size: px-to-rem(36);
    line-height: 110%;
    text-transform: uppercase;
    text-align: center;
    color: #62500f;

    & b {
      color: #ea8529;
    }
  }

  &__content-container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 20px;
  }

  &__pikers {
    display: flex;
    justify-content: space-between;
    width: 100%;
    position: absolute;
    top: 58%;

    &__block {
      display: flex;
    }
  }

  &__image-block {
    @include size(1056px);
    @include no-event;
    display: flex;
    flex-direction: column;
    align-items: center;

    @include toDesktop {
      @include size(816px);
    }

    @include toLaptop {
      @include size(600px);
    }

    @include toTablet {
      @include size(562px);
    }

    @include toPhone {
      @include size(318px);
    }
  }

  &__button-block {
    bottom: 14%;
    width: 380px;
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
  }

  @include toTablet {
    &__button-block {
      bottom: 0;
    }

    &__pikers {
      top: inherit;
      bottom: 12%;

      &__block {
        width: 38%;
        justify-content: space-between;
        gap: 20px;
      }
    }
  }

  @include toPhone {
    height: 370px;

    &__title-container {
      position: relative;
      margin-bottom: -45px;
      flex-direction: column;
      gap: 18px;
    }

    &__label {
      position: relative;
    }

    &__title {
      font-size: px-to-rem(20);
      place-self: center;
    }

    &__pikers {
      justify-content: center;
      gap: 10px;
      bottom: 15%;

      &__block {
        width: 50%;
        justify-content: center;
        gap: 10px;

        &:first-child {
          justify-content: end;
        }

        &:last-child {
          justify-content: start;
        }
      }
    }

    &__button-block {
      width: 100%;
    }
  }

  &__image {
    --width-image: 622px;
    width: var(--width-image);
    min-height: calc(var(--width-image) * 1.145);

    @include toDesktop {
      --width-image: 480px;
    }

    @include toLaptop {
      --width-image: 400px;
    }

    @include toTablet {
      --width-image: 300px;
    }

    @include toPhone {
      --width-image: 200px;
    }
  }

  &--blue {
    #{$origin}__title {
      color: var(--text-piro-ttle);

      & b {
        color: var(--text-piro-accent);
      }
    }
  }
}

.piker {
  $origin: &;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 15px;
  filter: drop-shadow(0 0 5px rgb(0 0 0 / 15.8%));
  cursor: pointer;

  &__border {
    @include min-size(140px);
    @include max-size(140px);
    @include flex-center;
    position: relative;
    border: 1px solid;
    border-radius: 100%;
    transition: all var(--transition-time);

    &--hidden {
      border-color: transparent !important;
    }
  }

  &__color {
    @include min-size(90px);
    position: absolute;
    border-radius: 100%;
    transition: all var(--transition-time);
  }

  &__title {
    font-family: var(--second-family);
    font-weight: 400;
    font-size: px-to-rem(16);
    line-height: 110%;
    text-align: center;
    color: #5d5955;
  }

  @include hover {
    #{$origin}__color {
      transform: scale(1.2);
    }
  }

  @include toDesktop {
    gap: 10px;

    &__border {
      @include min-size(110px);
      @include max-size(110px);
    }

    &__title {
      font-size: px-to-rem(14);
    }

    &__color {
      @include min-size(70px);
    }
  }

  @include toLaptop {
    &__border {
      @include min-size(100px);
      @include max-size(100px);
    }

    &__color {
      @include min-size(64px);
    }
  }

  @include toTablet {
    gap: 6px;

    &__border {
      @include min-size(54px);
      @include max-size(54px);
    }

    &__color {
      @include min-size(34px);
    }

    &__title {
      font-size: px-to-rem(14);
    }
  }

  @include toPhone {
    gap: 6px;

    &__border {
      @include min-size(36px);
      @include max-size(36px);
    }

    &__color {
      @include min-size(25px);
      @include max-size(25px);
    }

    &__title {
      display: none;
    }
  }
}

.gradient-fon {
  @include size(100%);
  position: absolute;
  filter: blur(300px);
  z-index: -1;

  &--blue {
    background: linear-gradient(
      135deg,
      rgb(247 250 255 / 60%) 0%,
      rgb(184 193 239 / 60%) 56.13%
    );
  }

  &--purple {
    background: linear-gradient(
      135deg,
      rgb(254 247 255 / 60%) 0%,
      rgb(226 184 239 / 60%) 56.13%
    );
  }

  &--milk {
    background: linear-gradient(
      135deg,
      rgb(255 253 247 / 60%) 0%,
      rgb(239 215 184 / 60%) 56.13%
    );
  }

  &--beige {
    background: linear-gradient(
      135deg,
      rgb(255 249 247 / 60%) 0%,
      rgb(239 203 184 / 60%) 56.13%
    );
  }

  &--orange {
    background: linear-gradient(
      135deg,
      rgb(255 251 247 / 60%) 0%,
      rgb(251 204 161 / 60%) 56.13%
    );
  }

  &--green {
    background: linear-gradient(
      135deg,
      rgb(253 255 247 / 60%) 0%,
      rgb(223 239 184 / 60%) 56.13%
    );
  }

  &--yellow {
    background: linear-gradient(
      135deg,
      rgb(255 254 247 / 60%) 0%,
      rgb(239 234 177 / 60%) 56.13%
    );
  }

  &--pink {
    background: linear-gradient(
      135deg,
      rgb(255 247 247 / 60%) 0%,
      rgb(239 189 184 / 60%) 56.13%
    );
  }

  &--white {
    background: linear-gradient(
      135deg,
      rgb(255 255 255 / 60%) 0%,
      rgb(232 232 232 / 60%) 56.13%
    );
  }
}
