.card-easy-11 {
  $origin: &;
  display: flex;
  flex-direction: column-reverse;
  justify-content: space-between;
  gap: 20px;
  height: 100%;
  padding: 44px;
  border-radius: 30px;
  background-color: var(--white);
  overflow: hidden;

  @include shadow;

  &__title {
    font-family: var(--font-family);
    font-size: px-to-rem(36);
    font-weight: 400;
    line-height: 120%;
    text-transform: uppercase;
    color: var(--text-talee-dark);

    & b {
      font-weight: 700;
      color: var(--text-talee-accent);
    }

    margin-bottom: 16px;
  }

  &__text {
    font-family: var(--second-family);
    font-weight: 400;
    font-size: px-to-rem(18);
    line-height: 130%;
    color: var(--text-talee-light);
    text-shadow: 0 0 24px 0 rgb(56 65 84 / 16%);
  }

  &__block {
    position: relative;
    display: flex;
    justify-content: space-between;
  }

  &__point {
    position: absolute;
    left: -44px;
    bottom: -90px;
    opacity: 0.3;
  }
}
