.title-block-3 {
  font-family: var(--font-family);
  font-weight: 400;
  font-size: px-to-rem(24);
  line-height: 120%;
  text-transform: uppercase;
  color: var(--text-talee-dark);
  padding: 18px 30px;

  &__text {
    font-family: var(--second-family);
    font-weight: 400;
    font-size: px-to-rem(18);
    line-height: 130%;
    color: var(--text-talee-light);
  }

  @include toDesktop {
    font-size: px-to-rem(20);
    padding: 16px 22px;
  }

  @include toPhone {
    font-size: px-to-rem(14);
  }
}

.title-block-5 {
  display: flex;
  flex-direction: column-reverse;
  gap: 10px;

  &__main-text {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: px-to-rem(28);
    line-height: 120%;
    color: #62500f;
    text-transform: uppercase;
  }

  &__text {
    font-family: var(--second-family);
    font-weight: 400;
    font-size: px-to-rem(18);
    line-height: 130%;
    color: var(--text-talee-light);
  }
}

.title-block-6 {
  display: flex;
  flex-direction: column-reverse;
  gap: 10px;

  &__main-text {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: px-to-rem(36);
    line-height: 120%;
    text-transform: uppercase;
    text-align: right;
    color: var(--white);
  }

  &__text {
    font-family: var(--second-family);
    font-weight: 400;
    font-size: px-to-rem(18);
    line-height: 130%;
    text-align: right;
    color: #fff8f0;
  }
}
