.card-easy-7 {
  $origin: &;
  width: 100%;
  max-width: 300px;

  &__number {
  }

  &__container {
    padding-bottom: 20px;
    border-bottom: 1px solid var(--primary-30p);
  }

  &__image {
  }

  &__title {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: px-to-rem(28);
    line-height: 130%;
    text-transform: uppercase;
    color: var(--text-talee-title);
  }

  &__text {
    font-family: var(--second-family);
    font-weight: 400;
    font-size: px-to-rem(18);
    line-height: 130%;
    color: var(--dark-gray);

    & > b {
      font-weight: 700;
      font-size: px-to-rem(24);
      color: var(--primary);
    }
  }

  &__block {
  }
}
