.title-block {
  display: flex;
  flex-direction: row-reverse;
  align-items: start;
  justify-content: space-between;
  padding: 18px 35px;
  gap: 16px;

  &__main-text {
    font-family: var(--font-family);
    font-weight: 500;
    font-size: px-to-rem(48);
    line-height: 108%;
    letter-spacing: 0.02em;
    text-transform: uppercase;
    color: var(--primary);
  }

  &__text {
    font-family: var(--second-family);
    font-weight: 400;
    font-size: px-to-rem(16);
    line-height: 130%;
    color: var(--text-talee-light);
  }

  // &__container {

  // }

  @include toDesktop {
    padding: 12px 15px;

    &__main-text {
      font-size: px-to-rem(40);
    }
  }
}
