.card-easy-4 {
  $origin: &;
  display: flex;
  gap: 20px;
  padding: 36px 32px;

  &__container {
    display: flex;
    flex-direction: column;

    // margin-bottom: 20px;
  }

  &__image {
    @include size(64px);
    @include min-size(64px);
    object-fit: contain;
    margin-top: 12px;
  }

  &__title {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: px-to-rem(28);
    line-height: 130%;
    letter-spacing: 0.01em;
    text-transform: uppercase;
    color: var(--text-tale-title);
    margin-bottom: 20px;
  }

  &__text {
    font-family: var(--second-family);
    font-weight: 400;
    font-size: px-to-rem(16);
    line-height: 130%;
    color: var(--text-talee-dark);
    opacity: 0.8;
    margin-bottom: 15px;
  }

  &__block {
    display: flex;
    flex-direction: column;
    align-items: start;
  }

  @include toPhone {
    flex-direction: column;
    padding: 22px 10px;

    &__image {
      @include size(40px);
      @include min-size(40px);
      margin-top: 0;
    }

    &__title {
      font-size: px-to-rem(16);
      margin-bottom: 12px;
    }

    &__text {
      font-size: px-to-rem(14);
    }

    &__block {
      display: flex;
      flex-direction: row-reverse;
    }
  }
}
