.title--mini {
  display: flex;
  flex-direction: column-reverse;
  text-align: start;

  &__number {
    font-size: px-to-rem(16);
    line-height: 18px;
    color: var(--light-blue);
    margin-bottom: 10px;
  }

  &__main-text {
    font-family: var(--second-family);
    font-size: px-to-rem(28);
    line-height: 28px;
    color: var(--dark-blue);
    margin-bottom: 20px;
    max-width: 200px;
  }

  &__text {
    font-size: px-to-rem(16);
    color: var(--text);
  }

  &__container {
    display: flex;
    flex-direction: column-reverse;
  }
}
