.title-block-2 {
  width: 100%;
  margin-block: -10px;
  display: flex;
  flex-direction: row-reverse;
  align-items: center;
  justify-content: start;
  padding: 24px;
  gap: 16px;

  &__number {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: px-to-rem(38);
    line-height: 113%;
    letter-spacing: 0.01em;
    text-transform: uppercase;
    color: var(--primary);
  }

  &__text {
    font-family: var(--second-family);
    font-weight: 400;
    font-size: px-to-rem(16);
    line-height: 130%;
    color: var(--text-tale-title);
  }

  // Effect
  $origin: &;

  &--blue {
    #{$origin}__number {
      color: var(--text-piro-accent);
    }

    #{$origin}__text {
      color: var(--text-piro-ttle);
    }
  }

  // Adaptive

  @include toDesktop {
    gap: 12px;
    padding: 5px 10px;
    margin: 0;
    height: max-content;

    &__number {
      font-size: px-to-rem(28);
    }

    &__text {
      font-size: px-to-rem(14);
    }
  }

  @include toLaptop {
    padding: 24px;
    height: 100%;
    flex-direction: column-reverse;
    justify-content: start;
    align-items: start;
  }

  @include toTablet {
    padding: 20px;
  }
}
