.section-main-slider {
  @include toDesktop {
    &__wrapper {
      padding-left: 100px;
    }

    &__switch {
      margin-bottom: 22px;

      &__item {
        font-size: px-to-rem(16);

        & > img {
          @include size(58px);
        }
      }
    }

    &__title {
      font-size: px-to-rem(74);
    }

    &__hr {
      margin-block: 20px;
    }

    &__text {
      font-size: px-to-rem(18);
      max-width: 340px;
    }

    &__picture {
      & .main-photo {
        @include size-important(808px);
      }
    }
  }

  @include toLaptop {
    &__switch {
      &__item {
        & > img {
          @include size(42px);
        }
      }
    }

    &__title {
      font-size: px-to-rem(48);
    }

    &__hr {
      margin-block: 20px;
    }

    &__container {
      grid-template-columns: 1fr;
    }

    &__picture {
      left: 10%;

      &__decor {
        left: -160px;
        top: 15px;
      }

      & .main-photo {
        @include size-important(636px);
      }

      & .button-accordion-simple {
        transform: scale(0.85);
      }
    }
  }

  @include toTablet {
    height: 450px;

    &__wrapper {
      padding-top: 35px;
      padding-left: 30px;
      grid-template-columns: 1.3fr 1fr;
    }

    &__switch {
      &__item {
        & > img {
          @include size(42px);
        }
      }
    }

    &__title {
      font-size: px-to-rem(36);
    }

    &__container {
      padding: 0;

      & .button {
        width: 65%;
      }
    }

    &__text {
      max-width: 100%;
      font-size: px-to-rem(16);
    }

    &__picture {
      left: -3%;

      &__decor {
        left: -125px;
        top: 25px;
      }

      & .main-photo {
        @include size-important(320px);
      }

      & .button-accordion-simple {
        transform: scale(0.7);
      }

      & .button-accordion-simple__container {
        @include size(34px);
        @include min-size(34px);
      }
    }
  }

  @include toPhone {
    height: 680px;

    &__wrapper {
      grid-template-columns: 1fr;
    }

    &__picture {
      // margin-top: 150px;
    }

    &__switch {
      &__item {
        & > img {
          @include size(42px);
        }
      }
    }

    &__title {
      font-size: px-to-rem(29);
    }

    &__empty {
      &--talee,
      &--piro {
        @include flex-center;
        height: 240px;
        width: 100%;
        padding-bottom: 30px;

        & .main-photo {
          @include size-important(320px);
        }

        & .button-accordion-simple {
          transform: scale(0.7);
        }

        & .button-accordion-simple__container {
          @include size(34px);
          @include min-size(34px);
        }
      }
    }

    &__hr {
      display: none;
    }

    &__container {
      padding: 0;

      & .button {
        width: 100%;
      }

      & .link {
        display: flex;
        width: max-content;
        place-self: end;
      }
    }
  }
}
