.screen {
  @include fullscreen;

  &--no-event {
    @include no-event;
  }

  &__container {
    @include size(100%);
    position: relative;

    & > * {
      position: absolute;
    }
  }

  &-desk {
    @include position-transform-symbol;
  }

  &-lab {
    @include toDesktop {
      @include position-transform-symbol;
    }
  }

  &-tab {
    @include toLaptop {
      @include position-transform-symbol;
    }
  }

  &-phone {
    @include toPhone {
      @include position-transform-symbol;
    }
  }
}
