.flex-column {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.grid-columns-1 {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 20px;
}

.grid-columns-2 {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 20px;
}

.grid-columns-3 {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 20px;
}

.grid-columns-4 {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 20px;
}

// 

@mixin generateOrders() {
  @for $index from -10 through 10 {
    &-#{$index} {
      order: $index;
    }
  }
}

._order {
  &-desk {
    @include generateOrders;
  }

  &-lab {
    @include toDesktop {
      @include generateOrders;
    }
  }

  &-tab {
    @include toLaptop {
      @include generateOrders;
    }
  }

  &-phone {
    @include toPhone {
      @include generateOrders;
    }
  }
}

