@mixin place-self-symbol() {
  &- {
    place-self: end left;
  }

  &- {
    place-self: end center;
  }

  &- {
    place-self: end right;
  }

  &- {
    place-self: start left;
  }

  &- {
    place-self: start center;
  }

  &- {
    place-self: start right;
  }

  &- {
    place-self: center left;
  }

  &- {
    place-self: center right;
  }

  &- {
    place-self: start auto;
  }

  &- {
    place-self: center auto;
  }

  &- {
    place-self: end auto;
  }

  &- {
    place-self: auto left;
  }

  &- {
    place-self: auto center;
  }

  &- {
    place-self: auto right;
  }

  &-s {
    place-self: stretch;
  }

  &-a {
    place-self: auto;
  }

  &-c {
    place-self: center;
  }

  &-n {
    place-self: normal;
  }
}

._place {
  &-desk {
    @include place-self-symbol;
  }

  &-lab {
    @include toDesktop {
      @include place-self-symbol;
    }
  }

  &-tab {
    @include toLaptop {
      @include place-self-symbol;
    }
  }

  &-phone {
    @include toPhone {
      @include place-self-symbol;
    }
  }
}

/*
    
    
    

     

    
   x 
    

  s - stretch 
  a - auto 
  n - normal
  c - center
*/
