.card-easy-3 {
  position: relative;
  padding: 26px 64px;

  &__container {
    display: flex;
    justify-content: start;
    align-items: center;
    gap: 15px;
    margin-bottom: 15px;
  }

  &__number {
    position: absolute;
    top: 26px;
    right: 64px;

    font-family: var(--font-family);
    font-weight: 400;
    font-size: px-to-rem(12);
    line-height: 130%;
    text-align: center;
    color: var(--primary-color-normal);
  }

  &__image {
    @include min-size(96px);
    @include size(96px);
  }

  &__title {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: px-to-rem(24);
    line-height: 130%;
    text-transform: uppercase;
    color: var(--text-tale-title);
  }

  &__block {
    display: flex;
    flex-direction: column;
    gap: 15px;
  }

  & hr {
    border-color: rgb(234 133 41 / 50%);
    border-width: 1px;
  }

  &__list {
    display: flex;
    flex-direction: column;
    gap: 12px;

    font-family: var(--second-family);
    font-weight: 400;
    font-size: px-to-rem(16);
    line-height: 130%;
    color: var(--text-talee-light);

    & b {
      font-weight: 900;
    }

    & > li {
      display: flex;

      &::before {
        @include min-size(8px);
        @include size(8px);
        content: "";
        display: block;
        border-radius: 100%;
        background-color: var(--primary);
        margin-top: 8px;
        margin-right: 12px;
      }
    }
  }

  @include toDesktop {
    padding: 20px 32px;
    padding-bottom: 50px;

    &__number {
      top: 20px;
      right: 32px;
    }
  }

  @include toLaptop {
    &__title {
      font-size: px-to-rem(22);
    }
  }

  @include toTablet {
    padding: 18px 10px;

    &__container {
      gap: 8px;
    }

    &__image {
      @include min-size(65px);
      @include size(65px);
    }

    &__title {
      font-size: px-to-rem(18);
    }
  }
}
