.menu {
  $origin: &;

  @include size(100%);
  position: relative;
  overflow: hidden;

  display: grid;
  grid-template-rows: 1fr max-content;

  border: 2px solid var(--surface-white-30);
  border-radius: 20px;

  background: rgb(255 255 255 / 80%) !important;
  -webkit-backdrop-filter: blur(8px) !important;
  backdrop-filter: blur(8px) !important;
  box-shadow: 0 0 14px 0 rgb(180 174 167 / 8%);
  z-index: 10;

  &__container {
    display: grid;
    grid-template-rows: max-content 1fr;
    overflow: hidden;
  }

  &__head {
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 40px;
    padding-bottom: 0;
    z-index: 1;

    margin-bottom: 32px;

    @include no-event;
  }

  &__list {
    display: flex;
    flex-direction: column;
    gap: 20px;
    overflow-y: scroll;
    padding-left: 40px;
    margin-right: 10px;
    padding-right: 10px;

    @include size-scrollbar(20px);
  }

  &__content {
    @include flex-center;
    flex-direction: column;
    position: relative;
    margin-bottom: 2px;
  }

  &__footer {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 24px;
    padding: 40px;
    padding-bottom: 0;
  }

  padding-bottom: 40px;

  &__title {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: px-to-rem(48);
    line-height: 120%;
    text-transform: uppercase;
    color: var(--text-talee-accent);
    text-shadow: 0 0 2px rgb(255 255 255 / 80%), 0 0 4px rgb(255 255 255 / 80%),
      0 0 6px rgb(255 255 255 / 80%), 0 0 8px rgb(255 255 255 / 80%),
      0 0 10px rgb(255 255 255 / 80%);
  }

  &__button {
    pointer-events: all;
  }

  &--list-grid {
    #{$origin}__list {
      display: grid;
      grid-template-columns: 1fr 1fr;
    }
  }

  &--list {
    #{$origin}__content {
      z-index: 2;

      @include no-event;
    }
  }

  &--blue {
    #{$origin}__title {
      color: var(--primary-blue);
    }

    #{$origin}__list {
      filter: hue-rotate(180deg);

      & img {
        filter: hue-rotate(180deg);
      }
    }
  }
}

@import "./menu-adaptive";
