.card-modal {
  $origin: &;
  position: relative;
  display: flex;
  justify-content: space-between;

  border: 1px solid rgb(93 89 85 / 20%);
  border-radius: 30px;
  padding: 32px;
  width: 100%;
  max-width: 694px;
  height: 280px;

  -webkit-backdrop-filter: blur(13px);
  backdrop-filter: blur(13px);
  box-shadow: 0 0 14px 0 rgb(180 174 167 / 19%);
  background: var(--surface-white-80);

  &__container {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 100%;
    max-width: 544px;
    gap: 16px;
  }

  &__title {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: px-to-rem(28);
    line-height: 120%;
    text-transform: uppercase;
    color: var(--text-tale-title);
    margin-right: 50px;

    // border: 1px solid;
  }

  &__text {
    font-family: var(--second-family);
    font-weight: 400;
    font-size: px-to-rem(18);
    line-height: 140%;
    color: var(--text-talee-light);
    padding-right: 86px;
    height: max-content;
    overflow-y: auto;

    @include size-scrollbar(10px);
  }

  &__block {
    position: absolute !important;
    top: 0;
    right: 0;
    padding: 24px;
  }

  @include toDesktop {
    padding: 20px;
    max-width: 380px;

    &__title {
      font-size: px-to-rem(20);
    }

    &__text {
      font-size: px-to-rem(14);
    }

    &__block {
      padding: 20px;
    }
  }

  @include toLaptop {
    padding: 20px;
    max-width: 330px;
    border-radius: 20px;

    &__title {
      font-size: px-to-rem(20);
    }

    &__text {
      font-size: px-to-rem(14);
    }

    &__block {
      padding: 20px;
    }
  }

  @include toPhone {
    padding: 18px 10px;
    height: 230px;

    // align-items: start;

    &__title {
      font-size: px-to-rem(16);
    }

    &__text {
      font-size: px-to-rem(14);
      padding-right: 10px;
    }
  }
}
