.card-easy-2 {
  padding: 14px 20px 14px 50px;

  &__container {
    display: grid;
    grid-template-columns: max-content 1fr;
    align-items: center;
    gap: 20px;
  }

  &__title {
    font-family: var(--font-family);
    font-weight: 500;
    font-size: px-to-rem(120);
    line-height: 108%;
    letter-spacing: 0.02em;
    color: var(--primary);
  }

  &__text {
    width: 100%;
    font-family: var(--second-family);
    font-weight: 400;
    font-size: px-to-rem(18);
    line-height: 127%;
    text-transform: uppercase;
    color: var(--text-tale-title);

    & > b {
      font-weight: 700;
    }

    & > span {
      font-weight: 700;
      color: var(--primary);
    }
  }

  $origin: &;

  @include toDesktop {
    padding: 35px 30px;

    #{$origin}__container {
      gap: 10px;
    }

    #{$origin}__title {
      font-size: px-to-rem(92);
    }

    #{$origin}__text {
      font-size: px-to-rem(16);
    }
  }

  @include toTablet {
    padding: 14px 24px;

    #{$origin}__container {
      grid-template-columns: 1fr;
    }

    #{$origin}__title {
      font-size: px-to-rem(44);
    }
  }

  @include toPhone {
    padding: 12px 20px;

    #{$origin}__title {
      font-size: px-to-rem(40);
    }

    #{$origin}__text {
      font-size: px-to-rem(14);
    }
  }

  &--blue {
    #{$origin}__title {
      color: var(--text-piro-accent);
    }

    #{$origin}__text {
      color: var(--text-piro-ttle);

      & span {
        color: var(--text-piro-accent);
      }
    }
  }
}
