.card-v {
  $origin: &;

  display: flex;
  flex-direction: column;
  justify-content: space-between;
  gap: 16px;

  padding: 32px;

  &__container {
    display: flex;
    flex-direction: row-reverse;
    align-items: center;
    justify-content: start;
    gap: 24px;
  }

  &__title {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: px-to-rem(28);
    line-height: 120%;
    text-transform: uppercase;
    color: var(--text-talee-title);

    & i {
      color: var(--primary);
    }
  }

  &__text {
    font-family: var(--second-family);
    font-weight: 400;
    font-size: px-to-rem(18);
    line-height: 130%;
    color: var(--text-talee-light);

    & i {
      font-weight: 700;
      color: var(--primary);
    }

    & b {
    }
  }

  &__block {
  }
}
