.wrapper-frame {
  $origin: &;
  display: flex;
  flex-direction: column;
  position: relative;
  border-radius: 20px;

  &::before {
    content: "";
    display: block;
    height: 20px;
    width: 100%;
    border: 2px solid rgb(234 133 41 / 25%);
    border-radius: 20px 20px 0 0;
    border-bottom: none;
    z-index: 1;
  }

  &__container {
    position: relative;
    height: 100%;
    margin-block: -20px;
    padding: 2px;
    border-radius: 25px;
    overflow: hidden;
  }

  &::after {
    content: "";
    display: block;
    height: 20px;
    width: 100%;
    border: 2px solid rgb(234 133 41 / 25%);
    border-radius: 0 0 20px 20px;
    border-top: none;
    z-index: 1;
  }

  &--bg {
    -webkit-backdrop-filter: blur(12px);
    backdrop-filter: blur(12px);
    box-shadow: 0 0 13px 0 rgb(180 174 167 / 22%);
    background: rgb(255 255 255 / 75%);
  }

  &--bg-2 {
    -webkit-backdrop-filter: blur(6px);
    backdrop-filter: blur(6px);
    box-shadow: 0 0 13px 0 rgb(180 174 167 / 22%);
    background: rgba(255, 255, 255, 0.449);
  }

  &--bg-orange {
    background: var(--primary-surface-white-hover);
  }

  &--bg-blue {
    background: var(--second-surface-white-hover);
  }

  &--height {
    height: 100%;
  }

  &--30 {
    border-radius: 30px;

    #{$origin}__container {
      margin-block: -30px;
      border-radius: 35px;
    }

    &::before {
      content: "";
      height: 30px;
      border: 2px solid rgb(234 133 41 / 50%);
      border-radius: 30px 30px 0 0;
      border-bottom: none;
    }

    &::after {
      content: "";
      height: 30px;
      border: 2px solid rgb(234 133 41 / 50%);
      border-radius: 0 0 30px 30px;
      border-top: none;
    }
  }

  &--40 {
    border-radius: 40px;

    #{$origin}__container {
      margin-block: -40px;
      border-radius: 45px;
    }

    &::before {
      content: "";
      height: 40px;
      border: 2px solid rgb(234 133 41 / 50%);
      border-radius: 40px 40px 0 0;
      border-bottom: none;
    }

    &::after {
      content: "";
      height: 40px;
      border: 2px solid rgb(234 133 41 / 50%);
      border-radius: 0 0 40px 40px;
      border-top: none;
    }
  }

  &--blue {
    &::before, &::after {
      border-color: var(--primary-blue);
      border-color: var(--primary-blue-30p);
    }
  }

  &--blue-20p {
    &::before, &::after {
      border-color: var(--primary-blue-20p);
    }
  }

  &--full {
    @include size(100%);
  }

  &--disabled {
    &::before,
    &::after {
      display: none;
    }
  }

  &--max-content {
    width: max-content;
  }
}

.wrapper-center {
  @include size(100%);
  @include flex-center;
  flex-direction: column;
}
