//       
@function fluid-size($min, $max, $min-screen: 320px, $max-screen: 1200px) {
  $slope: ($max - $min) / ($max-screen - $min-screen);
  $base: $min - $slope * $min-screen;

  @return calc(#{$base}px + #{$slope * 100}vw);
}

