/* -------------------------------------------------- */

//      rem
@function clm_rem($min, $width, $max) {
  $min_rem: #{$min + rem};
  $max_rem: #{$max + rem};
  $width_vw: #{calc(round(($max * 16) / ($width / 100) * 100000) / 100000) + vw};

  @return clamp($min_rem, $width_vw, $max_rem);
}

//   ,    2rem,
//  1.5rem    ,
//     600px

// font-size: clm_rem(1.5, 600, 2);

//    px
@function clm_px($min, $width, $max) {
  $min_px: #{$min + px};
  $max_px: #{$max + px};
  $width_vw: #{calc(round($max / ($width / 100) * 100000) / 100000) + vw};

  @return clamp($min_px, $width_vw, $max_px);
}

//       .
//   30px,  20px.
//       600px

// padding: clm_px(20, 600, 30);

@mixin padding-adaptive($min, $width, $max) {
  padding: clm_px($min, $width, $max);
}

@mixin margin-adaptive($min, $width, $max) {
  margin: clm_px($min, $width, $max);
}

/*
//  :

.card {
   @include padding-adaptive(20, 600, 30); // padding     
}
*/

/* -------------------------------------------------- */

//     ( 16px)
$base-font-size: 16;

//    px  rem
@function px-to-rem($px-value) {
  @return #{math.div($px-value, $base-font-size)}rem;
}

// font-size: px-to-rem(18);

@mixin font-size-adaptive(
  $size-desktop,
  $size-laptop: $size-desktop - 2px,
  $size-tablet: $size-laptop - 2px,
  $size-phone: $size-tablet - 2px,
) {
  font-size: px-to-rem($size-desktop);

  @include toLaptop {
    font-size: px-to-rem($size-laptop);
  }

  @include toTablet {
    font-size: px-to-rem($size-tablet);
  }

  @include toPhone {
    font-size: px-to-rem($size-phone);
  }
}

