@mixin position-transform-symbol() {
  &- {
    position: absolute;
    top: initial;
    right: initial;
    bottom: 0;
    left: 0;
    transform: none;
  }

  &- {
    position: absolute;
    top: initial;
    right: initial;
    bottom: 0;
    left: 50%;
    transform: translate(-50%, 0);
  }

  &- {
    position: absolute;
    top: initial;
