    bottom: initial;
    left: 50%;
    transform: translate(-50%, 0);
  }

  &- {
    position: absolute;
    top: 0;
    right: 0;
    bottom: initial;
    left: initial;
    transform: none;
  }

  &- {
    position: absolute;
    top: 50%;
    right: initial;
    bottom: initial;
    left: 0;
    transform: translate(0, -50%);
  }

  &- {
    position: absolute;
    top: 50%;
    right: 0;
    bottom: initial;
    left: initial;
    transform: translate(0, -50%);
  }

  &-c {
    position: absolute;
    top: 50%;
    right: initial;
    bottom: initial;
    left: 50%;
    transform: translate(-50%, -50%);
  }

  &-n {
    position: absolute;
    top: initial;
    right: initial;
    bottom: initial;
    left: initial;
    transform: none;
  }
}

@mixin size-scrollbar($size) {
  &::-webkit-scrollbar {
    @include size($size);
  }
}

/* -------------------------------------------------- */

@mixin container($min-width: null, $max-width: null) {
  @if $min-width != null and $max-width != null {
    @container (min-width: #{$min-width}) and (max-width: #{$max-width}) {
      @content;
    }
  } @else if $min-width != null {
    @container (min-width: #{$min-width}) {
      @content;
    }
  } @else if $max-width != null {
    @container (max-width: #{$max-width}) {
      @content;
    }
  }
}

