@import "./button-accordion-icon";
@import "./button-accordion-simple";
@import "./button-accordion-simple-2";

.button-accordion {
  $origin: &;

  @include flex-center;

  flex-direction: column;
  position: relative;
  z-index: 1;

  &__container {
    @include flex-center;
    gap: 20px;
    position: relative;
    cursor: pointer;
    border-radius: 35px !important;
    padding-inline: 56px;
    min-width: 340px;
    min-height: 67px;
    box-shadow: 0 0 10px 0 rgb(21 21 19 / 7%);
    background: var(--primary-surface-white);
    transition: var(--transition-time);

    &::after {
      @include size(15px);
      content: "";
      position: absolute;
      right: 26px;
      display: block;
      border-radius: 100% !important;
      background: var(--primary);
    }

    &--active {
      background: #ea8529;

      & > #{$origin}__title {
        color: #fff;
      }

      &::after {
        background: #fff;
      }
    }

    @include hover {
      box-shadow: 0 0 25px -10px var(--accent);
    }
  }

  &__title {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: px-to-rem(20);
    line-height: 90%;
    text-transform: uppercase;
    text-align: right;
    color: #62500f;
    -webkit-user-select: none;
    user-select: none;
  }

  &__pop-up {
    position: absolute;
    top: 80%;
    z-index: 2;
    width: max-content;
    transition: all var(--transition-time);

    &--hidden {
      @include no-event;
      opacity: 0;
      transform: scale(0);
      transform-origin: top;
    }
  }

  &--mirrow {
    #{$origin}__container::after {
      right: auto;
      left: 26px;
    }
  }

  /* Adaptive */

  @include toDesktop {
    &__container {
      min-width: 340px;
      min-height: 50px;
    }

    &__title {
      font-size: px-to-rem(18);
    }
  }

  @include toLaptop {
    &__container {
      min-width: 280px;
      min-height: 40px;

      &::after {
        @include size(10px);
      }
    }

    &__title {
      font-size: px-to-rem(16);
    }
  }

  @include toTablet {
    &__container {
      min-width: 220px;
      min-height: 38px;

      padding-inline: 18px 36px;

      &::after {
        @include size(10px);
      }
    }

    &__title {
      font-size: px-to-rem(16);
      text-align: start;
    }

    &--mirrow {
      #{$origin}__title {
        text-align: end;
      }
    }
  }
}
