.header {
  @include toDesktop {
    height: 80px;
    padding: 0 20px;
    
    &__container {
      justify-content: end;
    }

    &__navigation {
      display: none;
    }
  }

  @include toLaptop {
    height: 80px;
  }

  @include toTablet {
    height: 68px;
    padding: 0 15px;

    &__buttons {
      padding-left: 20px;
    }
    
    & a.button-icon,
    & .short-contacts__link--bigger {
      display: none;
    }
  }

  @include toPhone {
    height: 52px;

    &__button-icons {
      gap: 0;
    }

    &__buttons {
      padding-left: 10px;
    }
    
    &__hr,
    & .short-contacts__link,
    & button.button--light,
    & a.button-icon {
      display: none;
    }
  }
}
