.card-easy-12 {
  $origin: &;
  display: flex;
  flex-direction: column-reverse;
  justify-content: space-between;
  height: 100%;
  gap: 30px;
  padding: 44px;

  &__container {
    display: flex;
    flex-direction: column;
  }

  &__image {
    border-radius: 20px;
    width: 100%;
  }

  &__title {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: px-to-rem(36);
    line-height: 120%;
    text-transform: uppercase;
    color: var(--text-piro-ttle);
    margin-bottom: 8px;
  }

  &__text {
    font-family: var(--second-family);
    font-weight: 400;
    font-size: px-to-rem(24);
    line-height: 130%;
    color: var(--text-piro-light);
  }

  &__block {
    order: -1;
    margin-bottom: 30px;

    &__head {
      display: flex;
      align-items: center;

      &__number {
        font-family: var(--font-family);
        font-weight: 400;
        font-size: px-to-rem(48);
        line-height: 108%;
        letter-spacing: 0.02em;
        text-transform: uppercase;
        color: var(--second-material-300);

        margin-right: 50px;
      }

      &__line {
        height: 2px;
        background-color: var(--second-material-300);
        width: 100%;
        margin-right: -4px;
      }

      &__icon {
        @include flex-center;
      }
    }
  }
}
