.card-easy {
  $origin: &;

  padding: 32px;

  &__container {
    display: flex;
    margin-bottom: 20px;
    gap: 20px;
  }

  &__image {
    @include size(64px);
    @include min-size(64px);
    object-fit: contain;
  }

  &__text {
    font-family: var(--second-family);
    font-weight: 400;
    font-size: px-to-rem(16);
    line-height: 130%;
    color: var(--text-talee-dark);
    opacity: 0.8;
  }

  &__block {
    display: flex;
    flex-direction: column;
    align-items: end;
  }

  // Effects

  &--full {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100%;
    padding: 20px 32px;
  }

  &--blue {
    #{$origin}__image {
      filter: brightness(0.925) hue-rotate(180deg);
    }
  }
}
