* {
  box-sizing: border-box;
}

::selection {
  background-color: var(--light-blue-50p);
  color: var(--white);
}

::target-text {
  color: var(--primary);
  background-color: #fbe7d4;
  border-radius: 10px;
  display: inline-block;
}

html {
  color: var(--text);
  font-family: var(--font-family);
  font-weight: 400;
  font-size: px-to-rem(16);
  line-height: 110%;
}

hr {
  border-color: var(--text-tale-title-20);
}

a {
  /* (love hate) LVHA (link visited hover active) */
  $color: black;
  color: $color;
  text-decoration: none;

  &:link {
    color: $color;
  }

  &:visited {
    color: $color;
  }

  &:hover {
    /* text-decoration: underline */
  }

  &:active {
    color: $color;
  }
}

._link {
  font-size: px-to-rem(18);

  &:hover {
    text-decoration: underline;
  }

  @include toLaptop {
    font-size: px-to-rem(16);
  }
}

._no-event {
  @include no-event;
}

._relative {
  position: relative;
}

._absolute {
  position: absolute;
}
