.short-contacts {
  $origin: &;
  text-align: end;

  // margin-top: 22px;

  &__links {
    display: flex;
    flex-direction: column;
    gap: 8px;
  }

  &__link {
    display: flex;
    justify-content: start;
    align-items: center;
    font-family: var(--font-family);
    gap: 6px;
    font-size: px-to-rem(18);

    &:hover {
      text-decoration: underline;
    }

    &--bigger {
      font-size: px-to-rem(22);
      font-weight: 500;
      letter-spacing: 0.01em;
      color: var(--text-talee-dark);
    }

    color: var(--text-talee-dark);

    & img {
      @include size(18px);
    }

    & a:link,
    a {
      color: var(--text-talee-dark) !important;
    }
  }

  /* Adaptive */

  @include toDesktop {
  }

  @include toLaptop {
    &__link {
      font-size: px-to-rem(16);
    }
  }

  @include toTablet {
    &__link {
      font-size: px-to-rem(14);
    }
  }

  @include toPhone {
  }
}
