.section-map {
  &__title-container {
    margin-bottom: 20px;
  }

  &__content-container {
    position: relative;
    display: grid;
    height: calc(100dvh - 100px);
    max-height: 920px;
  }

  &__menu {
    position: absolute;
    top: 0;
    left: 0;
    place-self: left;
    z-index: 2;
  
    width: 764px;
    height: 100%;

    transition: var(--transition-time);

    &--hidden {
      @include no-event;
      opacity: 0;
      transform: translateX(-100px);
    }
  }

  &__map {
    --size-map: min(64vw, 1100px);

    position: absolute;
    place-self: center start;
    width: var(--size-map);
    height: calc(var(--size-map) * 0.676);

    margin-left: min(26vw, 350px);
  }

  $origin: &;

  @include toDesktop {
    #{$origin}__menu {
      width: 598px;
    }
  }

  @include toLaptop {
    #{$origin}__map {
    }
  }
  
  @include toTablet {
    &__content-container {
      height: 140vw;
      // max-height: max-content;

      transition: all var(--transition-time);
      
      &--hidden-menu {
        height: 64vw;
      }
    }

    #{$origin}__map {
      --size-map: 94vw;
      margin: 0;
      place-self: start center;
    }

    #{$origin}__menu {
      width: 100%;
      height: 65%;
      place-self: end center;

      top: auto;
      left: auto;
    }
  }
}
