.title-blue {
  display: flex;
  flex-direction: column-reverse;
  text-align: start;

  &__main-text {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: px-to-rem(36);
    line-height: 120%;
    text-transform: uppercase;
    color: var(--text-piro-dark);

    & b {
      font-weight: 700;
      color: var(--text-piro-accent);
    }
  }

  &__text {
    font-family: var(--second-family);
    font-weight: 400;
    font-size: px-to-rem(18);
    line-height: 130%;
    color: var(--text-piro-light);
  }

  &__container {
  }
}
