.section-accordion {
  $origin: &;
  border-bottom: 1px solid var(--text-tale-title-20);

  &__container {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 20px;
  }

  &__content {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }

  &__lable {
    display: flex;
    justify-content: space-between;
    gap: 100px;

    &__title {
      font-family: var(--font-family);
      font-weight: 400;
      font-size: px-to-rem(28);
      line-height: 120%;
      color: var(--text-talee-dark);
      padding: 30px;
    }
  }

  &__decor {
    position: relative;
    display: flex;
    justify-content: right;
    padding-top: 40px;
    padding-right: 65px;

    &__point {
      position: absolute;
    }

    &__block {
      position: relative;
    }

    &__card {
      position: absolute;
      top: 42%;
      left: -10%;
      width: 376px;
      transform: translateY(-50%);
    }

    &__image {
      position: relative;
      z-index: 1;
    }
  }

  &__list {
    display: flex;
    flex-direction: column;
    gap: 20px;
  }

  &__decor {
    transform: translateX(10%);
  }

  &--blue {
    filter: hue-rotate(180deg);

    #{$origin}__decor {
      &__image {
        filter: hue-rotate(170deg);
      }
    }
  }

  @include toDesktop {
    &__decor {
      transform: translateX(10%);
    }
  }

  @include toLaptop {
    &__decor {
      padding: 0;
    }
  }

  @include toTablet {
    &__container {
      grid-template-columns: 1fr;
    }

    &__lable {
      flex-direction: column;
      gap: 20px;
    }

    &__decor {
      transform: none;
    }
  }

  @include toPhone {
    &__decor {
      transform: translate(10%);

      &__block {
        height: 245px;
        display: flex;
      }

      &__card {
        top: 90px;
        left: -105px;
      }

      &__image {
        height: 246px;
        place-self: end;
        transform: translate(20px, 20px);
      }
    }

    &__list {
      gap: 8px;
    }
  }
}
