.card-easy-9 {
  $origin: &;

  display: flex;
  flex-direction: column-reverse;
  gap: 20px;

  &__container {
  }

  &__image {
    border-radius: 20px;
    width: 378px;
    height: 250px;
  }

  &__title {
    font-family: var(--second-family);
    font-weight: 700;
    font-size: px-to-rem(22);
    line-height: 130%;
    text-transform: uppercase;
    color: var(--text-talee-accent);
    margin-bottom: 10px;
  }

  &__text {
    font-family: var(--second-family);
    font-weight: 400;
    font-size: px-to-rem(16);
    line-height: 130%;
    color: var(--text-talee-dark);
  }
}
