.card-image-background {
  $origin: &;

  @include flex-center;
  position: relative;

  &__container {
    @include size(100%);
    display: flex;
    flex-direction: column-reverse;

    position: absolute;
    padding: 30px;
  }

  &__image {
    @include no-event;
    object-fit: cover;
    border-radius: 20px;
    min-height: 200px;
  }

  &__title {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: px-to-rem(24);
    line-height: 130%;
    text-transform: uppercase;
    color: var(--text-piro-white);

    & b {
      font-weight: 700;
    }
  }
}
