.form {
  $origin: &;
  padding: 24px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  gap: 20px;

  height: 100%;

  &__container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 20px;

  }

  @include toPhone {
    &__container {
      grid-template-columns: 1fr;
    }
  }
  
  &--form-1 {
    #{$origin}__container {
      @include toLaptop {
        grid-template-columns: 1fr;
      }
    }
  }
}
