@import "button-icon-service";

.button-icon {
  @include flex-center;
  @include min-size(44px);
  @include max-size(44px);

  border-radius: 100px;

  box-shadow: 0 3px 4px 0 rgb(31 33 29 / 11%);
  background: var(--text-talee-white);

  cursor: pointer;
  transition: all var(--transition-time);

  @include hover {
    transform: scale(1.1);
    box-shadow: 0 5px 10px 0 rgb(31 33 29 / 11%);
  }

  &--size-m {
    @include min-size(56px);
    @include max-size(56px);

    @include toDesktop {
      @include min-size(46px);
      @include max-size(46px);
    }

    @include toLaptop {
      @include min-size(44px);
      @include max-size(44px);
    }

    @include toTablet {
      @include min-size(38px);
      @include max-size(38px);
    }

    @include toPhone {
      @include min-size(30px);
      @include max-size(30px);
    }
  }
  
  &--border {
    @include min-size(56px);
    @include max-size(56px);
    border: 1px solid var(--primary);

    @include toDesktop {
      @include min-size(46px);
      @include max-size(46px);
    }

    @include toLaptop {
      @include min-size(44px);
      @include max-size(44px);
    }

    @include toTablet {
      @include min-size(38px);
      @include max-size(38px);
    }

    @include toPhone {
      @include min-size(30px);
      @include max-size(30px);
    }
  }

  &--no-bg {
    background: none;
    box-shadow: none;

    @include hover {
      transform: scale(1.1);
      box-shadow: 0 5px 10px 0 rgb(31 33 29 / 7%);
    }

    @include toPhone {
      & img {
        @include min-size(15px);
        @include max-size(15px);
      }

      @include min-size(30px);
      @include max-size(30px);
    }
  }

  // @include toDesktop {
  //   @include min-size(34px);
  //   @include max-size(34px);

  //   & > img {
  //     @include min-size(20px);
  //     @include size(20px);
  //   }
  // }
}
