.lable-main {
  font-family: var(--second-family);
  font-weight: 400;
  font-size: px-to-rem(12);
  line-height: 108%;
  letter-spacing: 0.01em;
  color: #5d5955;
  width: 130px;

  & > span {
    font-size: px-to-rem(16);
    line-height: 127%;
    text-transform: uppercase;
  }

  &--nowrap {
    text-wrap: nowrap;
  }

  &--light {
    color: var(--primary-surface-white);
  }

  &--blue {
    color: var(--text-piro-ttle);
  }

  @include toPhone {
    width: max-content;
  }
}
