.accordion {
  $origin: &;
  padding: 32px;
  overflow: hidden;
  height: max-content;

  &__title {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 30px;

    cursor: pointer;
    -webkit-user-select: none;
    user-select: none;

    font-family: var(--second-family);
    font-weight: 400;
    font-size: px-to-rem(22);
    line-height: 120%;
    color: var(--text-tale-title);
  }

  &__text {
    margin-top: 25px;
    padding-top: 25px;
    border-top: 1px solid rgb(98 80 15 / 30%);

    font-family: var(--second-family);
    font-weight: 400;
    font-size: px-to-rem(16);
    line-height: 130%;
    text-align: justify;
    color: var(--text-talee-light);

    transition: all var(--transition-time);

    &--hidden {
      height: 0;
      margin: 0;
      padding: 0;
      opacity: 0;
    }
  }

  &__icon {
    @include flex-center;
    @include min-size(64px);
    border-radius: 100%;
    background-color: #fff8f0;
    box-shadow: 0 0 4px 0 rgb(0 113 155 / 15%);
  }

  &__line {
    position: absolute;
    width: 16px;
    height: 2px;
    border-radius: 2px;
    background-color: var(--primary);
    transition: all var(--transition-time);

    &--plus {
      transform: rotate(-90deg);
    }
  }

  // Effects

  &--blue {
    padding-block: 20px;

    #{$origin}__title {
      font-family: var(--font-family);
      font-weight: 400;
      font-size: px-to-rem(24);
      line-height: 130%;
      text-transform: uppercase;
      color: var(--text-piro-ttle);

      & b {
        font-family: var(--font-family);
        font-weight: 400;
        font-size: px-to-rem(38);
        line-height: 113%;
        letter-spacing: 0.01em;
        text-transform: uppercase;
        color: var(--text-piro-accent);

        margin-right: 16px;
      }
    }

    #{$origin}__icon {
      background-color: var(--second-surface-white-hover);
    }

    #{$origin}__line {
      background-color: var(--text-piro-inactive);
    }
  }

  // Adaptive

  @include toPhone {
    padding: 15px 10px;

    &__title {
      font-size: px-to-rem(16);
      align-items: start;
      gap: 10px;
    }

    &__text {
      font-size: px-to-rem(14);
      margin-top: 10px;
      padding-top: 10px;
    }

    &__icon {
      @include min-size(30px);
    }
  }
}

.block-222 {
  display: flex;
  align-items: center;
  gap: 16px;

  padding: 20px;
  padding-inline: 32px;

  &__number {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: px-to-rem(38);
    line-height: 113%;
    letter-spacing: 0.01em;
    text-transform: uppercase;
    color: var(--text-piro-accent);
  }

  &__text {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: px-to-rem(24);
    line-height: 130%;
    text-transform: uppercase;
    color: var(--text-piro-ttle);
  }
}
