.object-photo {
  $origin: &;
  position: relative;

  &__link {
    position: absolute;
    display: block;
    left: -10%;
    bottom: 20%;
    padding-right: 40px;
    padding-bottom: 4px;
    border-bottom: 1px solid hsl(47deg 73% 22% / 50%);
    cursor: pointer;
    z-index: 1;

    &::after {
      @include no-event;
      content: "";
      display: block;
      position: absolute;
      border-bottom: 1px solid hsl(47deg 73% 22% / 50%);
      width: 50px;
      transform-origin: left bottom;
      left: 100%;
      bottom: -1px;
      transform: rotate(-30deg);
      transition: all var(--transition-time);
    }

    font-family: var(--font-family);
    font-weight: 500;
    font-size: px-to-rem(16);
    line-height: 80%;
    text-transform: uppercase;
    color: #62500f !important;

    transition: all var(--transition-time);

    @include hover {
      color: var(--primary) !important;
      border-bottom: 1px solid var(--primary-50p);

      &::after {
        border-bottom: 1px solid var(--primary-50p);
      }
    }
  }

  & > img {
    @include size(570px);
    position: relative;
    object-fit: contain;
    filter: drop-shadow(0 90px 50px rgb(16 15 14 / 17%));

    animation-name: anim-shlem-position;
    animation-duration: 4s;
    animation-iteration-count: infinite;
    animation-direction: alternate;
    animation-timing-function: cubic-bezier(0.175, 0.885, 0.32, 1.275);
  }

  & > .decor-blur-point {
    position: absolute;
    bottom: 0;
    background-color: rgb(0 0 0 / 65%) !important;

    animation-name: anim-shadow-under-shlem;
    animation-duration: 4s;
    animation-iteration-count: infinite;
    animation-direction: alternate;
    animation-timing-function: cubic-bezier(0.175, 0.885, 0.32, 1.275);
  }

  &--off-animation {
    & > img,
    & > .decor-blur-point {
      animation: none;
    }
  }

  &--main {
    & > img {
      @include size(710px);
    }

    @include toDesktop {
      & > img {
        @include size(540px);
      }

      #{$origin}__link {
        left: -2%;
        bottom: 8%;

        &::after {
          transform: rotate(-45deg);
          width: 55px;
        }
      }
    }

    @include toLaptop {
      & > img {
        @include size(430px);
      }

      #{$origin}__link {
        left: -13%;
        bottom: 5%;

        &::after {
          transform: rotate(-45deg);
          width: 55px;
        }
      }
    }

    @include toTablet {
      & > img {
        @include size(246px);
      }

      #{$origin}__link {
        left: -35%;
        bottom: -15%;

        &::after {
          transform: rotate(-65deg);
          width: 65px;
        }
      }
    }

    @include toPhone {
      #{$origin}__link {
        display: none;
      }
    }
  }
}

@keyframes anim-shlem-position {
  from {
    transform: translateY(2%);
  }

  to {
    transform: translateY(8%);
  }
}

@keyframes anim-shadow-under-shlem {
  from {
    opacity: 0.3;
    height: 1vw;
    width: 20vw;
  }

  to {
    opacity: 0.8;
    width: 28vw;
    height: 1.5vw;
  }
}
