.title-white {
  display: flex;
  flex-direction: column-reverse;
  text-align: start;
  gap: 8px;

  &__main-text {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: px-to-rem(36);
    line-height: 120%;
    text-transform: uppercase;
    color: var(--surface-white-80);
    text-shadow: 0 0 24px 0 rgb(56 65 84 / 16%);

    & b {
      font-weight: 700;
    }
  }

  &__text {
    font-family: var(--second-family);
    font-weight: 400;
    font-size: px-to-rem(18);
    line-height: 130%;
    color: var(--text-piro-white);
    text-shadow: 0 0 24px 0 rgb(56 65 84 / 16%);
  }

  &__container {
  }
}
