.menu {
  $origin: &;

  @include toDesktop {
    #{$origin}__title {
      font-size: px-to-rem(38);
    }

    #{$origin}__head {
      padding: 30px;
      padding-bottom: 0;
      margin-bottom: 25px;
    }

    #{$origin}__list {
      padding-left: 30px;
      padding-right: 7px;
      margin-right: 7px;

      @include size-scrollbar(16px);
    }

    #{$origin}__footer {
      padding: 30px;
      padding-bottom: 0;
    }

    padding-bottom: 30px;
  }

  @include toLaptop {
    #{$origin}__title {
      font-size: px-to-rem(36);
    }

    #{$origin}__head {
      padding: 20px;
      padding-bottom: 0;
      margin-bottom: 25px;
    }

    #{$origin}__list {
      padding-left: 20px;
      padding-right: 5px;
      margin-right: 5px;

      @include size-scrollbar(10px);
    }

    #{$origin}__footer {
      padding: 20px;
      padding-bottom: 0;
    }

    padding-bottom: 20px;
  }

  @include toTablet {
    #{$origin}__title {
      font-size: px-to-rem(28);
    }

    #{$origin}__head {
      padding: 15px;
      padding-bottom: 0;
      margin-bottom: 25px;
    }

    #{$origin}__list {
      padding-left: 15px;
      padding-right: 5px;
      margin-right: 5px;

      @include size-scrollbar(5px);
    }

    #{$origin}__footer {
      padding: 15px;
      padding-bottom: 0;
    }

    padding-bottom: 15px;
  }

  @include toPhone {
    #{$origin}__footer {
      flex-direction: column;
      align-items: end;
      padding: 15px;
      padding-bottom: 0;
    }
  }
}
