.header {
  @include gridSetting;

  $origin: &;

  align-items: center;
  border: 1px solid rgb(180 174 167 / 21%);
  border-radius: 0 0 18px 18px;
  padding: 0 30px;

  -webkit-backdrop-filter: blur(18px);
  backdrop-filter: blur(18px);
  box-shadow: 0 4px 11px 0 rgb(180 174 167 / 15%);
  background: rgb(255 253 248 / 80%);

  &__logo {
    width: 262px;
    height: 52px;
  
    @include toDesktop {
      width: 252px;
      height: 48px;
    }
  
    @include toLaptop {
      width: 280px;
      height: 55px;
    }
  
    @include toTablet {
      width: 198px;
      height: 40px;
    }
  
    @include toPhone {
      width: 145px;
      height: 30px;
    }
  }

  &__hr {
    width: 1px;
    height: 45px;
    background-color: #d0c8b1;
    margin: auto;
  }

  &__container {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__navigation {
  }

  &__button-icons {
    display: flex;
    gap: clm_px(0, 1920, 20);
  }

  &__buttons {
    display: flex;
    justify-content: end;
    gap: 20px;
    padding-left: 38px;
  }

  & .button-icon--no-bg img {
    transform: translateY(2px);
  }
}

@import "header-adaptive";
