.title-easy-2 {
  display: flex;
  flex-direction: column-reverse;
  text-align: start;
  gap: 4px;

  &__main-text {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: px-to-rem(28);
    line-height: 120%;
    text-transform: uppercase;
    color: var(--text-talee-title);
  }

  &__text {
    font-family: var(--second-family);
    font-weight: 400;
    font-size: px-to-rem(18);
    line-height: 130%;
    color: var(--text-talee-light);
  }
}
