.section-footer {
  padding-bottom: 74px;

  &__links {
    $origin: &;
    display: grid;
    grid-template-columns: 36fr 64fr;
    grid-template-rows: 1fr 1fr;
    gap: 20px;
    row-gap: 15px;

    &__title {
      font-family: var(--font-family);
      font-weight: 400;
      font-size: px-to-rem(30);
      line-height: 120%;
      letter-spacing: -0.01em;
      text-align: justify;
      color: #62500f;

      padding: 38px 90px;

      & > span {
        color: #ea8529;
      }

      @include toDesktop {
        font-size: px-to-rem(28);
        padding: 38px 40px;
      }

      @include toTablet {
        font-size: px-to-rem(22);
      }

      @include toPhone {
        font-size: px-to-rem(20);
        padding: 20px;
      }
    }

    &__social {
      padding: 38px 20px;
      gap: 25px;
    }

    @include toTablet {
      grid-template-columns: 1fr;
      grid-template-rows: max-content max-content max-content;
    }

    &--blue {
      #{$origin}__title {
        color: var(--text-piro-ttle);

        & span {
          color: var(--text-piro-accent);
        }
      }
    }
  }

  &__decor {
    display: flex;
    justify-content: center;
    margin-block: -120px -230px;
    overflow: clip;
  }

  &__footer {
    display: flex;
    justify-content: space-between;
    gap: 20px;

    @include toPhone {
      flex-direction: column;
      gap: 10px;
    }
  }
}

.card-footer {
  width: 100%;
  max-width: 484px;
  height: 150px;

  &__link {
    display: block;
    margin-bottom: 10px;
  }

  &__block {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 25px;
    margin: auto;

    text-align: end;
  }

  @include toLaptop {
  }
}

.section-links {
  display: grid;
  grid-template-columns: 2fr 1fr;
  gap: 130px;
  height: 100%;
  padding: 40px 50px;

  &__title {
    font-family: var(--font-family);
    font-weight: 300;
    font-size: px-to-rem(20);
    line-height: 100%;
    text-transform: uppercase;
    color: #5d5955;
    opacity: 0.5;

    padding-bottom: 18px;
  }

  &__hr {
    margin-bottom: 18px;
  }

  &__columns {
    width: 100&;
    display: flex;
    justify-content: space-between;
    padding-right: 66px;
  }

  @include toDesktop {
    gap: 50px;

    &__columns {
      padding-right: 0;
    }
  }

  @include toLaptop {
    padding: 35px 25px;
    gap: 0;
  }

  @include toTablet {
    grid-template-columns: 1fr;
    gap: 20px 10px;
  }
}

.list-of-links {
  display: flex;
  flex-direction: column;
  gap: 25px;
  width: 188px;

  @include toPhone {
    width: 100%;
  }
}
