.title-easy-3 {
  display: flex;
  flex-direction: column-reverse;
  text-align: start;
  gap: 4px;

  &__main-text {
    font-family: var(--second-family);
    font-weight: 400;
    font-size: px-to-rem(24);
    line-height: 130%;
    color: var(--text-talee-dark);
  }

  &__text {
    font-family: var(--second-family);
    font-weight: 400;
    font-size: px-to-rem(16);
    line-height: 130%;
    color: var(--text-talee-light);
  }
}
