.title-easy-4 {
  display: flex;
  flex-direction: column-reverse;
  text-align: start;

  &__main-text {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: px-to-rem(24);
    line-height: 130%;
    text-transform: uppercase;
    color: var(--text-talee-title);

    margin-bottom: 12px;
  }

  &__text {
    font-family: var(--second-family);
    font-weight: 400;
    font-size: px-to-rem(22);
    line-height: 120%;
    color: var(--text-talee-light);

    @include text-ellipsis-lines;
  }

  &__container {
  }
}
