.card-block-3 {
  $origin: &;

  display: flex;
  flex-direction: row;
  gap: 20px;
  border-radius: 30px;

  padding: 8px;
  -webkit-backdrop-filter: blur(7px);
  backdrop-filter: blur(7px);
  box-shadow: 0 0 18px 0 rgb(180 174 167 / 20%);
  background: rgb(255 255 255 / 58%);

  &__image {
    border: 2px solid rgb(234 133 41 / 25%) !important;
    border-radius: 30px;
    min-width: 296px;
    min-height: 308px;
    object-fit: cover;
  }

  &__container {
    display: grid;
    grid-template-rows: max-content max-content 1fr;
  }

  &__title {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: px-to-rem(24);
    line-height: 123%;
    letter-spacing: 0.01em;
    text-transform: uppercase;
    color: #ea8529;

    margin-bottom: 15px;
  }

  &__text {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: px-to-rem(18);
    line-height: 110%;
    color: #62500f;
    margin-bottom: 20px;
  }

  &__block {
    display: flex;
    flex-direction: column;

    justify-content: space-between;
  }

  @include toDesktop {
    gap: 8px;

    &__image {
      @include min-size(250px);
    }
  }

  @include toPhone {
    flex-direction: column;
    height: 484px;
    gap: 16px;
    justify-content: space-between;

    &__image {
      min-width: 100%;
      min-height: 246px;
    }

    &__title {
      font-size: px-to-rem(18);
      margin-bottom: 8px;
    }

    &__text {
      font-size: px-to-rem(16);
      margin-bottom: 8px;
    }

    &__block {
      display: flex;
      flex-direction: column;
      gap: 16px;
    }
  }
}
