.card-block-8 {
  $origin: &;

  display: flex;
  flex-direction: row;
  border-radius: 20px;
  padding: 0;

  -webkit-backdrop-filter: none;
  backdrop-filter: none;
  box-shadow: none;
  background: transparent;

  gap: 40px;

  &__container {
    padding-block: 10px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }

  &__image {
    @include min-size(214px);
    @include max-size(214px);
    border: 2px solid rgb(234 133 41 / 25%) !important;
    border-right: 2px solid transparent !important;
    border-left: 2px solid transparent !important;
    box-shadow: 0 0 14px 0 rgb(180 174 167 / 19%);
    border-radius: 20px;
  }

  &__title {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: px-to-rem(28);
    line-height: 120%;
    text-transform: uppercase;
    color: var(--text-talee-accent);
  }

  &__text {
    font-family: var(--second-family);
    font-weight: 400;
    font-size: px-to-rem(22);
    line-height: 120%;
    color: var(--text-talee-light);
  }
}
