.title {
  $origin: &;
  width: 100%;
  max-width: 700px;

  &__text {
    font-size: px-to-rem(20);
    line-height: 88%;
    letter-spacing: 0.02em;
    margin-bottom: 18px;
  }

  &__main-text {
    font-family: var(--second-family);
    font-style: italic;
    font-weight: 400;
    font-size: px-to-rem(46);
    line-height: 100%;
    letter-spacing: 0.02em;
    color: var(--dark-blue);
  }

  @include toTablet {
    &__main-text {
      font-size: px-to-rem(36);
    }
  }

  @include toPhone {
    &__text {
      font-size: px-to-rem(16);
      line-height: 114%;
      letter-spacing: 0.02em;
    }

    &__main-text {
      font-size: px-to-rem(27);
    }
  }

  &--second {
    #{$origin}__text {
      margin-bottom: 20px;
    }

    #{$origin}__main-text {
      font-size: px-to-rem(46);
    }

    @include toTablet {
      #{$origin}__main-text {
        font-size: px-to-rem(36);

        @include toPhone {
          font-size: px-to-rem(27);
        }
      }
    }
  }

  &--one-text {
    #{$origin}__text {
      font-family: var(--second-family);
      color: var(--dark-blue);
      text-align: center;
      font-size: px-to-rem(22);

      font-weight: 400;
      line-height: 120%;

      @include toPhone {
        font-size: px-to-rem(16);
      }
    }
  }

  &--font-family {
    #{$origin}__main-text {
      font-style: normal !important;
    }
  }
}

@import "./title--mini";
@import "./title--module";
@import "./title--circle-number";
@import "./title-simple";
@import "./title-block";
