.title-easy-22 {
  display: flex;
  flex-direction: column-reverse;
  text-align: start;

  &__main-text {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 54px;
    line-height: 115%;
    letter-spacing: 0.01em;
    text-transform: uppercase;
    color: var(--text-piro-dark);
    
    & > b {
      font-weight: 400;
      color: var(--text-piro-accent);
    }
  }

  &__text {
    font-family: var(--second-family);
    font-weight: 400;
    font-size: 18px;
    line-height: 130%;
    color: var(--text-piro-light);
  }

  &__container {

  }
}
