.title-main {
  $origin: &;
  display: flex;
  flex-direction: column;
  gap: 2px;

  font-family: var(--font-family);
  font-weight: 400;
  font-size: px-to-rem(54);
  line-height: 121%;
  letter-spacing: 0.01em;
  text-transform: uppercase;
  color: #62500f;

  &__item {
    display: inline-block;
    max-width: 95%;

    &:first-child {
      place-self: end;
    }
  }

  & b {
    font-weight: 400;
    border: 1px solid var(--primary);
    border-radius: 100px;
    display: inline-block;
    text-wrap: nowrap;

    padding-inline: 10px;
    margin-inline: -10px;

    @include toLaptop {
      padding-inline: 7px;
      margin-inline: -7px;
    }

    @include toTablet {
      padding-inline: 5px;
      margin-inline: -5px;
    }

    @include toPhone {
      padding-inline: 3px;
      margin-inline: -3px;
    }
  }

  & strong {
    font-weight: 500;
  }

  & i {
    color: #ea8529;
    font-style: normal;
  }

  &--light {
    color: var(--primary-surface-white);

    & b {
      border: 1px solid var(--primary-surface-white);
    }
  }

  &--small {
    font-size: px-to-rem(36);
  }

  &--first {
    #{$origin}__item {
      &:first-child {
        place-self: start;
      }
    }
  }

  &--float-right {
    #{$origin}__item {
      place-self: end;
    }
  }

  &--center {
    #{$origin}__item {
      place-self: center;
    }
  }

  &--blue {
    color: var(--text-piro-ttle);

    & b {
      border: 1px solid var(--text-piro-accent);
    }

    & i {
      color: var(--text-piro-accent);
    }
  }

  /* Adaptive */

  @include toDesktop {
    font-size: px-to-rem(46);
  }

  @include toLaptop {
    font-size: px-to-rem(36);
  }

  @include toTablet {
    font-size: px-to-rem(28);
  }

  @include toPhone {
    font-size: px-to-rem(20);
  }
}
