.map {
  @include size(100%);
  position: relative;
  margin: auto;

  &__container {
    @include no-event;
    @include fullscreen;
    z-index: 1;
  }

  &__list {
    @include size(100%);
    position: relative;
  }

  &__item {
    display: flex;
    align-items: start;
    gap: 6px;
    position: absolute;
    font-family: var(--font-family);
    font-weight: 400;
    font-size: clm_rem(0.5, 1400, 1.3);

    line-height: 110%;
    text-transform: uppercase;
    color: var(--surface-white-80);
    border-inline: 10px solid transparent;
    transform: translateX(-10px);

    &::before {
      @include size(10px);
      content: "";
      display: inline-block;
      margin-top: 6%;
      border-radius: 50%;
      background-color: var(--surface-white-80);
    }

    &--active {
      color: var(--primary);
      transition: var(--transition-time);
      background-color: #fbe7d4;
      border-color: #fbe7d4;

      border-radius: 4px;

      &::before {
        @include size(10px);
        content: "";
        display: inline-block;
        margin-top: 6%;
        border-radius: 50%;
        background-color: var(--primary);
      }
    }
  }

  &__cursor {
    @include no-event;
    position: fixed;
    display: none !important;

    padding: 2px;
    z-index: 999;

    font-size: px-to-rem(18);
    text-shadow: 1px 1px 3px rgb(50 50 50 / 100%);
    color: #fbe7d4;

    &::before {
      content: "";
      width: 0;
      height: 0;
      border-top: 8px solid transparent;
      border-right: 8px solid var(--primary);
      border-bottom: 8px solid transparent;
      position: absolute;
      border-radius: 50px;
      left: -10px;
      top: 2px;
      filter: drop-shadow(0 1px 1px rgb(50 50 50 / 50%));
    }
  }

  & svg {
    filter: drop-shadow(0 0 20px #ea8629a9);
    position: absolute;
    top: 0;
    left: 0;
    cursor: alias;
  }

  &--no-region svg {
    cursor: default;
  }

  & path {
    transition: opacity 0.3s, stroke-width 0.3s;
    cursor: grab;

    &:active {
      cursor: grabbing;
    }
  }

  $origin: &;

  &--blue {
    filter: hue-rotate(180deg);
  }

  @include toLaptop {
    #{$origin}__item {
      // font-size: px-to-rem(15);
    }
  }
}
