.section-main-slider {
  position: relative;
  height: 770px;

  /* ---------------------------------- */
  &__decor-1 {
    position: absolute;
    top: 0;
    left: 0;
  }

  &__decor-2 {
    position: absolute;
    bottom: 0;
    left: 30%;
  }

  /* ---------------------------------- */

  &__wrapper {
    position: absolute;
    display: grid;
    grid-template-columns: 1fr 1fr;
    padding-block: 60px 115px;
    padding-left: 120px;
    margin-left: -30px;

    transition: all 0.6s;

    &--hidden {
      @include no-event;
      opacity: 0;
    }
  }

  &__content {
    position: relative;
  }

  &__switch {
    max-width: max-content;
    margin-bottom: 30px;
    position: relative;
    z-index: 1;

    &__line {
      position: relative;
      display: flex;
      flex-direction: column;
      justify-content: center;
      height: 12px;
    }

    &__point {
      @include size(12px);

      position: absolute;
      border-radius: 100%;
      background-color: #ea8529;

      top: 0;
      left: 18%;
      z-index: 1;

      transition: all 0.6s cubic-bezier(0.64, -0.41, 0.39, 1.42);

      &--right {
        left: 78%;
        background-color: var(--primary-blue);
      }
    }

    &__container {
      display: flex;
      align-items: end;
      gap: 30px;
      margin-bottom: 12px;
    }

    &__item {
      @include flex-center;
      flex-direction: column;
      gap: 4px;

      font-family: var(--font-family);
      font-weight: 400;
      font-size: px-to-rem(18);
      letter-spacing: 0.01em;
      color: #5d5955;

      cursor: pointer;
      transition: all var(--transition-time);
      border: 1px solid transparent;
      border-bottom: none;
      border-inline: none;
      border-radius: 35px;

      & > img {
        @include size(70px);
        object-fit: contain;
        opacity: 0;
        transform: scale(0.75);
        transition: all var(--transition-time);
      }

      &--active {
        color: #ea8529;

        & > img {
          opacity: 1 !important;
          transform: scale(1);
        }

        &-right {
          color: var(--primary-blue);

          & > img {
            opacity: 1 !important;
            transform: scale(1);
          }
        }
      }

      @include hover {
        border-color: var(--white);
        transform: scale(1.05) translateY(-1px);
        background: radial-gradient(
          circle,
          rgb(255 255 255 / 10%) 1%,
          rgb(0 0 0 / 0%) 60%
        );

        filter: drop-shadow(0 0 2px var(--white))
          // drop-shadow(0 0 8px var(--white))
          drop-shadow(0 0 16px var(--white));

        & > img {
          opacity: 0.25;
        }
      }
    }

    &__hr {
      height: 24px;
      width: 1px;
      background-color: var(--text-tale-title);
      opacity: 0.3;
    }
  }

  &__title {
    position: relative;
    z-index: 1;

    font-family: var(--font-family);
    font-weight: 400;
    font-size: px-to-rem(84);
    line-height: 110%;
    letter-spacing: 0.02em;
    text-transform: uppercase;
    color: #62500f;

    text-wrap: nowrap;

    // white-space: nowrap;

    & b {
      font-weight: 500;
      line-height: 101%;
      color: var(--primary);
    }
  }

  &__empty {
    &--talee {
      position: relative;
      display: none;
    }
  }

  &__hr {
    margin-block: 45px;
  }

  &__container {
    display: grid;
    grid-template-columns: 2fr 1.5fr;
    align-items: end;
    gap: 24px;
    padding-right: 36px;

    & .link {
      display: none;
    }
  }

  &__text {
    max-width: 420px;
    font-family: var(--second-family);
    font-weight: 400;
    font-size: px-to-rem(22);
    line-height: 120%;
    color: #5d5955;

    & span {
      font-weight: 700;
      color: #ea8529;
    }

    &--blue {
      width: 80%;

      & span {
        color: var(--primary-blue);
      }
    }
  }

  &__picture {
    @include flex-center;
    position: relative;

    &__decor {
      position: absolute;
      top: 40px;
      left: -150px;
    }

    &__info-container {
      @include no-event;
      @include size(100%);

      // position: relative;
      z-index: 3;

      & > * {
        position: absolute;
        pointer-events: all;
      }
    }

    &__info {
      &--1 {
        top: 15%;
        left: 38%;
      }

      &--2 {
        top: 36%;
        left: 54%;
      }

      &--3 {
        top: 77%;
        left: 59%;
      }

      @include toPhone {
        &--1 {
          left: 60%;
        }

        &--2 {
          top: 45%;
          left: 50%;
        }
      }

      // ---

      &--4 {
        top: 63%;
        left: 28%;
        z-index: 3;
      }

      &--5 {
        top: 80%;
        left: 53%;
        z-index: 3;
      }

      &--6 {
        top: 45%;
        left: 67%;
      }
    }

    &__object > img {
      @include size(710px);
    }
  }
}

@import "section-main-slider-adaptive";
