.section-more-about-talee {
  &__block-1 {
    background-image: url("../images/decor/decor_2.jpg");
    background-size: cover;
    min-height: 300px;
    border-radius: 30px;
    padding: 50px;

    display: flex;
    flex-direction: column-reverse;

    &__container {
      display: grid;
      grid-template-columns: 1fr 1fr;
      gap: 20px;
    }
  }

  &__block-2 {
    display: flex;
    flex-direction: column;
    gap: 20px;

    padding: 40px;
    border-radius: 30px;

    -webkit-backdrop-filter: blur(12px);
    backdrop-filter: blur(12px);
    box-shadow: 0 0 14px 0 rgb(180 174 167 / 19%);
    background: rgb(255 255 255 / 90%);
  }

  &__content {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 20px;
  }

  &__text {
    font-family: var(--second-family);
    font-weight: 400;
    font-size: px-to-rem(18);
    line-height: 130%;
    color: var(--text-talee-light);
  }

  &__image-text {
    display: flex;
    flex-direction: column-reverse;
    padding: 30px;

    font-family: var(--second-family);
    font-weight: 700;
    font-size: px-to-rem(16);
    line-height: 130%;
    text-transform: uppercase;
    text-align: start;
    color: #fffefd;

    background-image: url("../images/decor/decor_3.jpg");
    background-size: cover;
    border-radius: 20px;
  }

  &__title {
    font-family: var(--second-family);
    font-weight: 400;
    font-size: px-to-rem(24);
    line-height: 130%;
    text-transform: uppercase;
    color: var(--text-tale-title);

    & b {
      font-weight: 700;
      color: var(--text-talee-accent);
    }
  }

  @include toDesktop {
    &__title {
      font-size: px-to-rem(20);
    }

    &__text {
      font-size: px-to-rem(16);
    }

    &__block-1 {
      padding: 15px;
    }

    &__block-2 {
      padding: 20px;
      gap: 10px;
    }

    &__content {
      gap: 10px;
    }
  }

  @include toLaptop {
    &__block-1 {
      &__container {
        grid-template-columns: 1fr;

        & > div {
          height: 100px;
        }
      }
    }

    &__content {
      grid-template-columns: 1fr;
      gap: 10px;

      &--2 {
        grid-template-columns: repeat(4, 1fr);
      }
    }

    &__image-text {
      height: 180px;
    }

    &__last {
      margin-top: -40px;
    }

    &__hr {
      display: none;
    }
  }

  @include toTablet {
    &__block-1 {
      height: 315px;

      &__container {
        grid-template-columns: 1fr 1fr;

        & > div {
          height: 100%;
        }
      }
    }

    &__content {
      grid-template-columns: 1fr 1fr;

      &--2 {
        grid-template-columns: repeat(2, 1fr);
      }
    }
  }

  @include toPhone {
    &__block-1 {
      height: 270px;

      &__container {
        grid-template-columns: 1fr;

        & > div {
          min-height: 100px;
        }
      }
    }

    &__content {
      grid-template-columns: 1fr;

      &--2 {
        grid-template-columns: repeat(1, 1fr);
      }
    }

    &__image-text {
      padding: 12px;
      font-weight: 400;
    }
  }
}
